.stores {

  &__item {
    @extend .flex, .flex--center, .flex--center-y;
    flex-direction: column;

  }

  &__image-box {
    position: relative;
    margin-bottom: 30px;
    width: 100%;
    height: 170px;
    padding: 10px;
    border: 1px solid $border-color;
    margin-top: 40px;


    @include hover() {
      @include transition();
      .image-container{
        transform: scale(1.1);
        -moz-transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -o-transform: scale(1.1);
        -ms-transform: scale(1.1); /* IE 9 */
        -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=1.5, M12=0, M21=0, M22=1.5, SizingMethod='auto expand')"; /* IE8 */
        filter: progid:DXImageTransform.Microsoft.Matrix(M11=1.1, M12=0, M21=0, M22=1.1, SizingMethod='auto expand'); /* IE6 and 7 */
      }
    }


    .image-container{
      position: relative;
      width: 100%;
      height: 100%;
      background-position: center;
      background-repeat: no-repeat;
      z-index: 0;
      transition: all 1s ease;
      -moz-transition: all 1s ease;
      -ms-transition: all 1s ease;
      -webkit-transition: all 1s ease;
      -o-transition: all 1s ease;
      @include transition();

      img {
        object-fit: scale-down;
      }
    }
  }

  &__title {
    text-transform: uppercase;
    font-size: 10px;
    color: $key-color-1;
    letter-spacing: 2px;
    margin-bottom: 5px;
    margin-top: 5px;
  }

  &__name {
    font-size: 18px;
    color: $blue-color;
    margin-bottom: 10px;
  }

  &__address {
    text-align: center;
    font-size: 18px;
    font-weight: $default-font-weight-light !important;
    color: $heading-color !important;
  }

  &__phone, &__email {
    font-size: 18px;
    font-weight: $default-font-weight-light !important;
    color: $heading-color !important;
    @include transition();

    @include hover() {
      color: $key-color-1 !important;
      padding-left: 10px;
      @include transition();
    }
  }


}



/* Styles for stores displayed via accordion */
.stores_page {
    position: relative;
    z-index: 1;
}

.stores_page {
	.stores__name {
	  font-size: 1rem;
	}

	.stores__name_void {
	  color: #333;
	  cursor: default;
	  &:hover {
		color: #333;
		cursor: default;
	  }
	}

	.stores__address, .stores__email, .stores__phone {
	  font-size: 1rem;
	  display: block;
	}

	.stores__item {
	  position: relative;
	}


	.stores__image-box {
	  display: flex;
	  align-items: center;
	  flex-direction: column;
	  justify-content: space-around;
	  img {
		max-height: 32px;
		width: auto;
		transition: all 0.5s;
	  }
	}

	div.stores__image-box:hover div {
	  color: #333;
	}

	.stores__image-box {
	  &:hover img {
		-webkit-transform: scale(1.2);
		-moz-transform: scale(1.2);
		-o-transform: scale(1.2);
	  }
	  div {
		color: #333;
		transition: all 0.5s;
		text-align: center;
	  }
	  &:hover div {
		color: #3fbf3f;
	  }
	  div {
		&:nth-child(1) {
		  height: 32px;
		}
		&:nth-child(2) {
		  font-weight: 500;
		}
		&:nth-child(3) {
		  font-size: 0.9rem;
		}
	  }
	}

	.stores__item_btn2 {
	  margin: -30px 0 20px 0;
	  background: #efefef;
	  width: 100%;
	  text-align: center;
	  > div {
		padding: 8px;
		height: 40px;
		display: block;
		&:hover i {
		  color: #3fbf3f;
		}
	  }
	  .accordion-btn--alt {
		> .ico:first-child {
		  display: inline-block;
		  margin: 0 5px 0 0;
		  position: relative;
		  top: 1px;
		  font-size: 12px;
		}
		.accordion-btn__title {
		  display: inline-block;
		  > span {
			font-size: 13px;
		  }
		}
	  }
	}

	.stores__item_info2 {
	  box-shadow: 0px 2px 3px #ccc;
	  text-align: center;
	  margin-top: -22px;
		width: 100%;
		padding: 0 10px;
	}
}