.product {

  &__image {
    display: flex;
    align-items: center;
    justify-content: center;

	&_wrapper {
	  position: relative;
	  .sticker {
		position:absolute;
		max-width:130px;
		max-height:130px;
		z-index: 1;
	  }
	  .sticker_lt {
		left:0;
		top:0;
	  }
	  .sticker_rt {
		right:0;
		top:0;
	  }
	  .sticker_rb {
		right:0;
		bottom:0;
	  }
	  .sticker_lb {
		left:0;
		bottom:0;
	  }
	}

    @include max-screen($sm-max) {
      margin-top: 40px;
      margin-bottom: 40px;
    }
  }

  &__heading {
    font-size: 42px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    margin-bottom: 50px;
    text-align: left;
    line-height: $small-line-height;

    @include max-screen($xxs-max) {
      font-size: 26px;
    }

    h1,h2,h3,h4,h5,h6 {
      font-size: inherit;
      font-weight: inherit;
      color: inherit;
      margin: 0;
    }
  }

  &__heading-icons {
    text-align: center;
    margin-bottom: 30px;

    img {
      margin: 0 15px;
    }
  }

  &__description {
    font-size: 18px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    margin-bottom: 50px;
    text-align: left;
    line-height: 1.5;

    @include max-screen($xxs-max) {
      font-size: 16px;
    }

    h1,h2,h3,h4,h5,h6, p {
      font-size: inherit;
      font-weight: inherit;
      color: inherit;
      margin: 0;
    }

    &--center {
      text-align: center !important;
    }
  }

  &__contact-info {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;

    .ico {
      font-size: 24px;
      padding-right: 10px;
    }
  }

  &__features {
    padding: 0 0 0 20px;
    /*display: flex;*/
    height: 100%;
    font-size: 18px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    @include max-screen($sm-max) {
      align-items: center;
      width: 100%;
    }

    @include max-screen($xxs-max) {

    }

    span.green {
      font-weight: $default-font-weight;
      font-size: 24px;
      color: $key-color-1;

      @include max-screen($xxs-max) {
        font-size: 18px;
      }

    }

    a {
      color: $blue-color;
      @include transition();
      @include max-screen($xxs-max) {
        font-size: 30px;
      }


      @include hover(){
        color: $key-color-1;
        @include transition();
      }
    }
  }

  &__text-icons {
    text-align: center;
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-weight: $default-font-weight-light;
    padding: 0 40px;
    color: $heading-color;
    margin-top: 40px;

    p.lead {
      font-weight: $default-font-weight;
      margin: 0;
    }

    i {
      color: $key-color-1;
      font-size: 40px;
    }

    &--noPadding {
      padding: 0 !important;
    }

    &--iconSmall {
      i {
        font-size: 20px !important;
      }
    }

  }

  &__logo {
    width: 100%;

  }

  &__title {
    font-size: 36px;
    font-weight: $default-font-weight-light;
    color: $heading-color !important;
    text-align: left;
    line-height: $small-line-height;
    width: 100%;

    @include max-screen($xxs-max) {
      font-size: 26px;
    }

    h1,h2,h3,h4,h5,h6 {
      font-size: inherit;
      font-weight: inherit;
      color: inherit;
      margin: 0;
    }
  }

  &__subtitle {
    margin-bottom: 20px;
    width: 100%;
    text-align: left;
    a:link, a:visited, a:hover {
      color: #2d2c2c;
      font-size: 23px;
      font-weight: 300;
    }
  }

  &__text {

    @include max-screen($xxs-max) {
      font-size: 14px;
    }
  }


  &__wrapper {
    position: relative;
    padding: 80px 0 60px 0;
    border-bottom: 1px solid $border-color;

    @include max-screen($xs-max) {
      padding: 40px 0;
    }
/*todo    ,    , */
    .arrow {
      position: absolute;
      width: 30px;
      right: 30px;
      bottom: 30px;
      content: '';
      @include transition();

      @include hover() {
        right: 20px;
        @include transition();
      }
    }


  }

  &__price-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: -30px;
    margin-bottom: 30px;
  }

  &__buy {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: right;

    @include max-screen($xxs-max) {
      flex-direction: column;
      align-items:flex-end;
    }
  }

  &__more {
    display: inline-block;
    margin-left: 20px;
    margin-top: 20px;
  }

  &__price {
    font-size: 36px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    display: inline-block;
    vertical-align: middle;
    padding-right: 12px;

    > span {
      font-size: 15px;
      font-weight: $default-font-weight;
    }
    > span.previous {
      font-size: 17px;
      font-weight: $default-font-weight-light;
      margin-right: 5px;
      text-decoration: line-through;
      color: #8a8787;
    }

    @include max-screen($xxs-max) {
      font-size: 26px;
    }
  }
  &__price-main {
    font-size: 36px;
    font-weight: 300;
    color: #2d2c2c;
    display: inline-block;
    vertical-align: middle;

    @include max-screen($xxs-max) {
      font-size: 26px;
    }

    .discount {
        font-size: 24px;
        text-decoration: line-through;
    }
  }

  &__wrap {
    margin-top: 20px;
  }

  &__btn {
    display:inline-block;
  }

  &__navigation {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 20px;
    font-size: 16px;
    font-weight: $default-font-weight-light;
    color: $heading-color;

    @include max-screen($sm-max) {
      justify-content: space-around;
    }

    @include max-screen($xxs-max) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    a {
      color: inherit;
      font-weight: inherit;
    }

    &-item {
      padding: 5px 0;
      margin: 0 20px;
      border-bottom: 3px solid transparent;
      @include transition();

      @include max-screen($sm-max) {
        margin: 0;
      }

      @include max-screen($xxs-max) {
        margin: 5px 0;
      }

      @include hover() {
        font-weight: $default-font-weight;
        color: $key-color-1;
        border-bottom: 3px solid $key-color-1;
        @include transition();
      }


      &.is-active {
        font-weight: $default-font-weight;
        color: $key-color-1;
        border-bottom: 3px solid $key-color-1;
      }
    }
  }

  &__add-to-compare {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    opacity: 0;

    > a {
      font-size: 14px;
      color: $key-color-2 !important;
    }
    > a:hover {
      color: $key-color-1 !important;
    }

    @include max-screen($xs-max) {
      margin-top: 10px;
      margin-bottom: 40px;
    }

  }


  &__bottom-text {
    color: $heading-color;
    font-size: 16px;
    font-weight: $default-font-weight;
    text-align: center;

    > span, span.italic {
      font-style: italic;
    }
  }


}

.product__navigation-item a::after {
  display: block;
  content: attr(data-title);
  font-weight: bold;
  height: 0;
  overflow: hidden;
  visibility: hidden;
}

.rouble {
  font-size: 15px;
  font-weight: 400;
}


.icon-text-product {
  padding: 50px 0 100px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include max-screen($sm-max) {
    padding: 50px;
  }

  @include max-screen($xxs-max) {
    padding: 20px;
  }

  &__icon {
    background-color: #ffffff;
    //border: 1px solid $border-color;
    width: 82px;
    height: 82px;
    border-radius: 41px;
    color: $key-color-1;
    font-size: 20px;
    padding: 0;
    margin-bottom: 40px;

    @include transition();

    /* position */
    display: flex;
    align-items: center;
    justify-content: center;

    i {
      color: inherit;
      padding: 0;
      font-weight: $default-font-weight-bold;
      font-size: 30px;
    }


    @include hover() {
      background-color: $key-color-1;
      color: #ffffff;
      @include transition();
    }
  }

  &__text {
    color: $heading-color;
    padding: 0 10px;
    font-size: 16px;
    font-weight: $default-font-weight;
    text-align: center;
    width: 100%;
  }
}


.specs {
  font-size: 16px;
  font-weight: $default-font-weight-light;
  color: $heading-color;
  line-height: 1.3;
  border-bottom: 1px solid $border-color;
  padding: 40px 0;

  @include max-screen($xxs-max) {
    text-align: center;
  }

  .row {
	padding: 4px 0;
  }

  &__title {
    font-size: 36px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    text-align: left;
    line-height: 1;

    @include max-screen($sm-max) {
      text-align: left;
      margin-bottom: 50px;
    }

    @include max-screen($xxs-max) {
      text-align: center;
      margin-bottom: 50px;
    }


    h1,h2,h3,h4,h5,h6 {
      font-size: inherit;
      font-weight: inherit;
      color: inherit;
      margin: 0;
    }

    &--compare {
      margin-bottom: 50px;
    }
  }

  span.specs2 {
    font-size: 16px;
    font-weight: $default-font-weight;
  }

  &__info {
    margin-top: 40px;
    font-size: 15px;
    font-weight: $default-font-weight-light;
    font-style: italic;
    color: $key-color-3;
    opacity: 0.6;

    p {
      margin-bottom: 10px;
    }
  }
}

html[lang="de"] .specs__title {
  font-size:24px;
}

.maps {

  &__text1 {
    font-size: 18px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    text-align: center;
    margin-bottom: 40px;

    h1,h2,h3,h4,h5,h6 {
      font-size: inherit;
      font-weight: inherit;
      color: inherit;
      margin: 0;
    }
  }

  &__text2 {
    font-size: 14px;
    font-weight: $default-font-weight;
    color: $heading-color;
    text-align: center;
    margin-bottom: 40px;
    padding: 0 20px;

    h1,h2,h3,h4,h5,h6 {
      font-size: inherit;
      font-weight: inherit;
      color: inherit;
      margin: 0;
    }
  }
}


.flag {

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;

  &__item {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 20px;
    min-width: 215px;

    @include max-screen($md-max) {
      min-width: 215px;
    }

    @include max-screen($sm-max) {
      min-width: 215px;
    }

    @include max-screen($xxs-max) {
      padding-left: 30px;
    }
  }

  &__img {

  }

  &__name {
    padding-left: 20px;
    font-size: 15px;
    font-weight: $default-font-weight;
    color: $heading-color;
  }
}



/* video */


.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px; height: 0; overflow: hidden;
}

.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}




/*     :
/navigation/android
/navigation/blackberry
/navigation/iphoneipad
/navigation/windowsphone */

.navigation__table {
  border-left: 1px solid #e5e5e5;
  border-top: 1px solid #e5e5e5;
  > div {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    > div {
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      -webkit-align-items: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      border-right: 1px solid #e5e5e5;
      border-bottom: 1px solid #e5e5e5;
      padding: 12px;
      &:nth-child(1) {
        width: 20%;
        justify-content: center;
      }
      &:nth-child(2) {
        width: 80%;
        > div {
          display: block;
        }
        span {
          display: block;
          font-weight: bold;
        }
      }
    }
  }
}

.navigation__bottom_block {
  margin: 40px 0 0 0;
}

.navigation__bottom_buy {
  font-size: 20px;
  display: block;
}

.navigation__bottom_man {
  margin: 20px 0;
  img {
    height: 60px;
    width: auto;
  }
}

.navigation__bottom_text {
  line-height: 1.8;
}

@media screen and (max-width: 575px) {
  .navigation__table > div {
    display: block;
    > div {
      display: block;
      &:nth-child(1) {
        width: 100%;
        border-bottom: none;
        padding-bottom: 0;
      }
      &:nth-child(2) {
        width: 100%;
      }
    }
  }
}