/*
 * 	Default theme - Owl Carousel CSS File
 */

$color-base:			$key-color-1 !default;
$color-white:			#FFF !default;
$color-gray:			#D6D6D6 !default;
$color-dark:			$default-font-color !default;

//nav

$nav-color: 			$color-dark !default;
$nav-color-hover:		$color-white !default;
$nav-font-size: 		30px !default;
$nav-rounded: 			36px !default;
$nav-margin: 			0 15px !default;
$nav-padding: 			6px 10px !default;
$nav-background: 		$color-white !default;
$nav-background-hover:	$color-base !default;
$nav-disabled-opacity: 	0.5 !default;

//dots

$dot-width:				8px !default;
$dot-height:			8px !default;
$dot-rounded:			50% !default;
$dot-margin: 			10px !default;
$dot-background:		$key-color-2 !default;
$dot-background-active:	$key-color-2 !default;

.owl-carousel {
	// Styling Next and Prev buttons

	.owl-nav {
		z-index: 100;
		position: absolute;
		width: 100%;
		top: 50%;
		@include translate(0,-50%);
		text-align: center;
		-webkit-tap-highlight-color: transparent;
		pointer-events: none;
		display: flex;
		justify-content: space-between;
		
		[class*='owl-'] {
			color: $nav-color;
			font-size: $nav-font-size;
			margin: $nav-margin;
			padding: $nav-padding;
			width: 72px;
			height: 72px;
			background: $nav-background;
			box-shadow: 1px 6px 20px 2px hsla(0,0%,41%,.1);
			display: inline-flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
			border-radius: $nav-rounded;
			pointer-events: auto;
			//border: 1px solid $border-color;
			@include transition();

			.ico-chevron-left {
				display: block;
				transform: rotate(180deg);
			}

			&:hover {
				//background: $nav-background-hover;
				//color:$nav-color-hover;
				//text-decoration: none;
				box-shadow: none;
				@include transition();
			}
		}
		
		.disabled {
			opacity: $nav-disabled-opacity;
			cursor: default;
		}
	}

	// Styling dots
	.owl-nav.disabled + .owl-dots {
		//margin-top: 10px;
	}

	.owl-dots {
		z-index: 100;
		position: relative;
		width: 100%;
		bottom: 20px;
		text-align: center;
		-webkit-tap-highlight-color: transparent;
		pointer-events: none;
		display: flex;
		align-items: center;
		justify-content: center;
		
		.owl-dot {
			display: block;
			zoom: 1;
			*display: inline;
			pointer-events: auto;
			
			span {
				width: $dot-width;
				height: $dot-height;
				margin: $dot-margin;
				background: $dot-background;
				display: block;
				-webkit-backface-visibility: visible;
				border-radius: $dot-rounded;
				@include transition();
			}

			&.active,
			&:hover {
				span {
					width: $dot-width +8px;
					height: $dot-height +7px;
					background: $dot-background-active;
				}
			}
		}
	}
}



.slider {
	position: relative;
	
	&__item {
		position: relative;
		width: 100%;
		color: #FFF;
		font-size: 5rem;
		text-align: center;
		display: flex;
		align-items: flex-end;
		justify-content: center;
		background-repeat: no-repeat;
		background-position: left;
		background-size: cover;
		
		> .container {
			margin-left: 0;
			margin-right: 0;
			position: absolute;
			bottom: 50px;

			@include max-screen($md-max) {
				bottom: 32px;
			}
		}

	}

	&__update {
		position: absolute;
		bottom: 20px;
		right: 100px;
		cursor: pointer;
		z-index: 1;

		@include max-screen($sm-max) {
		};
		@include max-screen($xs-max) {
			display: none;
		};

		> .icon-text__icon {
			i {
				font-size: 30px;
			}
		}
	}

	&__button {

		a.btn {
			padding-top: 12px;
			padding-right: 40px !important;
			padding-bottom: 12px;
			padding-left: 40px !important;
		}
		@include max-screen($xs-max) {
			display: none;
		}
	}
}


.slider-products {
	 position: relative;

	 .owl-item {
		 opacity: 0;
		 .slider-products__wrapper-button {
			 opacity: 0;
		 }

		 &.active {
			 opacity: 1;
			 .slider-products__wrapper-button {
				 opacity: 1;
			 }
		 }
	 }

	 .owl-nav {
		 width: 100%;
		 padding: 0 10%;

		 @include max-screen($md-max) {
			 padding: 0 5%;
		 }

		 @include max-screen($xs-max) {
			 display: none;
		 }
	 }



	 &__item {
		 width: 100%;
		 padding: 50px 0 0 0;
		 text-align: center;
		 display: flex;
		 align-items: center;
		 justify-content: space-between;
		 max-width: 1200px;
		 margin: 0 auto;

		 @include max-screen($xs-max) {
			 padding: 30px 20px;
		 }

		 @include max-screen($xxs-max) {
			 flex-direction: column;
		 }

	 }

	 &__image{
		 //width: 50%;
		 width: 40%;
		 text-align: right;

		 img {
			 display: inherit !important;
			 width: 270px !important;
		 }
	 }

	 &__wrapper {
		 width: 50%;
		 display: flex;
		 padding: 0 39px 0 0;
		 flex-direction: column;
		 justify-content: flex-start;
		 align-items: flex-start;

		 @include max-screen($sm-max) {
			 width: 70%;
			 padding-left: 50px;
		 }

		 @include max-screen($xxs-max) {
			 padding: 0;
			 width: 100%;
			 margin-top: 50px;
		 }


		 &-title {
			 font-size: 13px;
			 font-weight: $default-font-weight-medium;
			 color: $default-font-color;
			 letter-spacing: $letter-spacing-wider;
		 }
		 &-name {
			 font-size: 51px;
			 font-weight: $default-font-weight-light;
			 color: $heading-color;
			 margin-bottom: 50px;
			 a {color: #2d2c2c;}
		 }
		 &-lead {
			 font-size: 18px;
			 font-weight: $default-font-weight;
			 color: $heading-color;
			 text-align: left;
			 margin-bottom: 40px;
			 a {color: #2d2c2c;}
		 }
		 &-text {
			 font-size: 15px;
			 font-weight: $default-font-weight;
			 color: $default-font-color;
			 text-align: left;
			 margin-bottom: 50px;
			 max-width: 430px;
			 a {color: #818080;}
		 }
		 &-button {

		 }
	 }
 }


.slider-products-page {
	position: relative;

	.owl-item {
		opacity: .3;

		&.active {
			opacity: 1;
		}
	}

	.owl-nav {
		width: 100%;
		padding: 0 10%;

		@include max-screen($md-max) {
			padding: 0 5%;
		}

		@include max-screen($xs-max) {
			display: none;
		}
	}

	.owl-dots {
		z-index: 100;
		position: relative;
		width: 100%;
		bottom: 0px;
		text-align: center;
		-webkit-tap-highlight-color: transparent;
		pointer-events: none;
		display: flex;
		align-items: center;
		justify-content: center;

		@include max-screen($sm-max) {
			bottom: -20px;
		}


		.owl-dot {
			display: block;
			zoom: 1;
			*display: inline;
			pointer-events: auto;

			span {
				width: $dot-width;
				height: $dot-height;
				margin: $dot-margin;
				background: $key-color-1;
				display: block;
				-webkit-backface-visibility: visible;
				border-radius: $dot-rounded;
				@include transition();
			}

			&.active,
			&:hover {
				span {
					width: $dot-width +8px;
					height: $dot-height +8px;
					background: $key-color-1;
				}
			}
		}
	}



	&__item {
		width: 100%;
		padding: 100px;
		height: 500px;
		text-align: center;
		display: flex;
		align-items: center;
		justify-content: space-between;
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
		position: relative;

		@include max-screen($sm-max) {
			height: 400px;
		}

		@include max-screen($xs-max) {
			height: 300px;
		}

		@include max-screen($xxs-max) {
			height: 200px;
			flex-direction: column;
		}
	}

	&__label {
		position: absolute;
		top:0;
		right: -30px;
		width: 100px;
		height: 100px;
		border-radius: 50px;
		background-color: $key-color-1;
		color: #ffffff;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		text-transform: uppercase;
		font-size: 26px;
		font-weight: $default-font-weight-medium;
		transform: rotate(-12deg);

		span {
			font-size: 12px;
			font-weight: $default-font-weight;
			text-transform: none;
		}

		@include max-screen($xs-max) {
			top: 5px;
			right: 15px;
			width: 80px;
			height: 80px;
			font-size: 20px;
			span {
				font-size: 10px;
				font-weight: $default-font-weight;
				text-transform: none;
			}
		}

		@include max-screen($xs-max) {
			top: 5px;
			right: 15px;
			width: 70px;
			height: 70px;
			font-size: 18px;
			span {
				font-size: 8px;
				font-weight: $default-font-weight;
				text-transform: none;
			}
		}
	}
}

