.login {



  &__wrapper {

  }

  &__restore-password {
    color: $blue-color;
    text-align: center;
    display: block;
    padding: 5px;
    font-size: 14px;
    font-weight: $default-font-weight-light;
  }

  &__title {
    font-size: 24px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    text-align: center;
    margin-bottom: 20px;
  }

  &__social {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    margin-bottom: 20px;

    &-item {
      width: 100%;
      max-width: 150px;
      padding: 10px 0;
      background-color: #0d95e8;
      border-radius: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 2px;

      .ico {
        color: #ffffff;
        font-size: 25px;
      }

      &--facebook {
        background-color: #3b5998;

        @include hover() {
          background-color: lighten(#3b5998, 10%);
        }
      }

      &--vk {
        background-color: #45668e;

        @include hover() {
          background-color: lighten(#45668e, 10%);
        }
      }

      &--google {
        background-image: url(/assets/img/icons/google_auth.svg);
        background-color: initial;
        background-position: center;
        background-repeat: no-repeat;
        border: 1px solid #3367d6;
      }
    }
  }

  &__line {
    width: 1px;
    height: 100%;
    background-color: $border-color;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.line-in-middle {
  width:100%;
  height:80%;
  background: linear-gradient(to right,
          transparent 0%,
          transparent calc(50% - 0.6px),
          $border-color calc(50% - 0.6px),
          $border-color calc(50% + 0.6px),
          transparent calc(50% + 0.6px),
          transparent 100%);
}
