.accordion {
    /* hide for smooth toggle blocks
    overflow: hidden;
    max-height: 0;
    transition: all .5s ease;
    &.is-active {
        overflow: visible !important;
        max-height: 100%;
    }*/
    display: none;

    &-section {
    }


    &__list {
        li {
            @extend .flex, .flex--center-y, .flex--space-between, .flex--nowrap;
            width: 100%;
            padding: 30px;
            background-color: #fff;
            margin-bottom: 2px;
        }
        &-title {
            font-size: 18px;
            color: $default-font-color;
        }
        &-subtitle {
            flex: 1 0 auto;
            text-align: right;
            font-size: 18px;
            color: $key-color-1;
            padding-left: 15px;
            > span {
                font-size: 13px;
                color: $default-font-color;
            }
        }
    }



    .accordion__inner {

        @include max-screen($xs-max) {
            overflow: auto;
        }


        > p {
            &:last-child {
                margin-bottom: 0;
            }
        }

        &-image {
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            width: 100%;
            height: 150px;
        }
    }

    &--alt {
        .accordion__inner {
            padding: 10px 0 20px 0;
            border-bottom: 1px solid rgba($heading-color, .15);
            > p {

                font-family: $default-font-family;
                font-weight: $default-font-weight-light;
                font-size: 18px;
                color: $heading-color;

                > a {
                    color: $key-color-1;
                }

                > a:hover {
                    color: $link-color-hover;
                }


                &:last-child {
                    margin-bottom: 0;
                }
            }

            > ol {
                color: $heading-color;
            }

            .img_wrap {padding: 0 0 15px 0;}

            &-image {
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
                width: 100%;
                height: 150px;
            }
        }
    }
}

.accordion-btn {
    @extend .flex, .flex--center-y, .flex--space-between, .flex--nowrap;
    position: relative;
    width: 100%;
    padding: 20px 30px;
    background-color: #f3faf6;
    color: #000;
    //margin-bottom: 30px;

    cursor: pointer;
    @include transition();
    @include hover() {
        background-color: darken(#f3faf6, 10%);
    }


    > .ico:first-child {
        display: block;
        @include transition();
    }
    > .ico:last-child {
        display: none;
        @include transition();
    }


    &.is-active {
        background-color: $key-color-1;
        color: #ffffff;

        .accordion-btn__title {
            > label {
                .show {
                    display: none;
                }

                .hide {
                    display: flex;
                }
            }
        }

        > .ico,
        > .ico-default {
            transform: rotate(180deg);
            color: #ffffff;
            font-size: 14px;
            @include transition();
        }
    }
    &__title {
        @extend .flex;
        > label {
            position: absolute;
            right: 60px;
            font-size: 16px;
            font-weight: 400;

            .show {

            }
            .hide {
                display: none;
            }
        }

        > span {
            font-size: 16px;
            font-weight: $default-font-weight-medium;
            @include transition();
        }
    }
    > .ico,
    > .ico-default {
        font-size: 14px;
        color: $key-color-1;
        font-weight: $default-font-weight;
        margin-left: 15px;
        @include transition();
    }
    &--alt {
        padding: 20px 0;
        background-color: transparent;
        border-bottom: 1px solid rgba($heading-color, .15);


        > .ico:first-child {
            display: block;
            @include transition();
        }
        > .ico:last-child {
            display: none;
            @include transition();
        }

        > .ico,
        > .ico-default {
            color: $heading-color;
        }

        @include hover() {
            background-color: transparent;
            @include transition();

            .accordion-btn__title {
                > span {
                    color: $key-color-1;
                    @include transition();
                }
            }

            > .ico,
            > .ico-default {
                //color: #ffffff;
            }

        }
        &.is-active {
            border-bottom-color: transparent;
            background-color: transparent;

            > .ico,
            > .ico-default {
                color: $key-color-1;
            }

            .accordion-btn__title {
                > span {
                    color: $key-color-1;
                    @include transition();
                }

            }
        }
        .accordion-btn__title {
            @extend .flex, .flex--column;
            > label {
                color: $default-font-color;
                font-size: 12px;
                font-weight: 400;
                margin-top: 15px;
            }
            > span {
                color: $heading-color;
                font-size: 24px;
                font-weight: $default-font-weight-light;
                @include transition();

                @include max-screen($xxs-max) {
                    font-size: 16px;
                }
            }
        }
        > .ico,
        > .ico-default {
            //color: rgba($heading-color, .5);
        }
    }
}

