.mondevice {

  &__list {
    li {
      margin-bottom: 3px;
    }
  }

  &__img{
    text-align: center;
    border: 1px solid $border-color;
    margin: 20px 0;
    img {

    }
  }

  &__title {
    color:$heading-color;
    padding-right: 60px;
    color: #2d2c2c;
    display: block;
    position:relative;
    margin: 20px;
    &::after {
      content: "\e020";
      font-family: $default-font-family-icon !important;
      speak: none;
      font-style: normal;
      font-weight: 100;
      font-variant: normal;
      text-transform: none;
      line-height: 1;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      position: absolute;
      top:50%;
      transform: translateY(-50%);
      right:0px;

    }
  }

  .accordion-btn {
    padding: 13px 30px;
    background-color: $key-color-4;
    color: $heading-color;

    &__title {
      & > span {
        font-size: $default-accordion-font-size;
        font-weight: $default-font-weight-light;
      }

     & > label {
        line-height: 38px;
      }

    }

    &.is-active {
      background-color: $border-color;
      color: $heading-color;


      > .ico,
      > .ico-default {
        color: $heading-color;
      }

    }

    > .ico,
    > .ico-default {
      color: $heading-color;
      margin-left: 15px;
      @include transition();
    }

  }
  .accordion__inner {
    @include max-screen($xs-max) {
      overflow: visible;
    }
  }

}


.pad__cell {
  padding: 1.25rem; }