#device_title {
    padding: 40px 0 20px 0;
}

#device_stick {
    position: absolute;
    width: 100%;
    background: #fbfbfb;
    padding: 0;

	.site-nav {padding: 0;}

	&.stick_fixed {
		position: fixed;
		z-index: 2;
		box-shadow: 0 2px 15px rgba(0,0,0,.1);
		@include min-screen($xs-min) {
			top:90px;
		}
		@include max-screen($xxs-max) {
			top:70px;
		}
	}

	@include min-screen($xs-min) {
		padding: 7px 0 5px;
    }
}
#device_stick_offset {padding:0;}


#device_product {
	@include min-screen($xs-min) {
		padding-top: 160px;
	}
}




/*      #desc.   ,        ,     . */
/* start */
#desc {
  .land {
    padding: 0px 27px;
    h5 {
	  font-weight: 600;
      margin: 15px auto 10px;
    }
    p {
      text-align: justify;
    }
    .row {
      margin: 0;
    }
    .row-clr {
      background-image: url('/assets/img/catalog/900x500px_grey_to_white.jpg');
    }
    .row-map {
      background-image: url('/assets/img/catalog/900x500px_gradient_map.jpg');
    }
    .flex {
      padding: 0;
      align-content: center;
      max-height: 250px;
      overflow: hidden;
	  @include max-screen($sm-min) {
		justify-content: center;
	  }
    }
    .flex-rgt {
      justify-content: flex-end;
    }
    .flex-txt {
      display: block;
      padding: 20px !important;
      p {
        margin: 0;
      }
      @include max-screen($sm-min) {
        justify-content: left;
        max-height: inherit !important;
      }
    }
  }
  .row-cnt {
    text-align: center;
    min-height: auto;
    .flex {
      justify-content: center;
    }
  }
  .land-sort {
    .row {
      &:nth-child(2n) {
        flex-direction: row-reverse;
      }
      &:nth-child(2n+1) {
        flex-direction: row;
      }
	}
  }
}
/* end   */



/*     (#desc) */
#desc {
	.landing {
		overflow: hidden;
		> div {
			margin-bottom: 100px;
			background-size: auto;
			background-repeat: no-repeat;
			background-position: center top;
			overflow: hidden;
			@include max-screen($lg-min) {
				margin-bottom: 0px;
			}
			@include max-screen($sm-min) {
				min-height: auto;
				background-image: none !important;
			}
		}
		> div > div {
			max-width: 1920px;
			margin: 0 auto;
			display: block;
			> img {
				display: block;
				margin: 0 auto;
				@include max-screen($sm-min) {display:none;}
			}
		}
		.img {
		    max-width: 700px;
			min-height: 720px;
			max-height: 720px;
			line-height: 720px;
			margin-top: 0px;
			margin-bottom: 20px;
			img {
				max-width: 100%;
				max-height: inherit;
			}
			@include max-screen($sm-min) {
				display: none;
				max-width: 100%;
				min-height: 100%;
				max-height: 100%;
				line-height: 0;
				margin: 0;
			}
		}
		.img_center {
			max-width: 1200px;
			margin: 0 auto;
			text-align: center;
			img {
				width: 100%;
				max-height: inherit;
			}
			@include max-screen($sm-min) {
				display: none;
			}
		}
		.img_left {
			float: left;
			margin-left: 150px;
			@include max-screen($lg-min) {
				margin-left: 20px;
			}
			@include max-screen($sm-min) {
				display: none;
			}
		}
		.img_right {
			float: right;
			margin-right: 150px;
			@include max-screen($lg-min) {
				margin-right: 20px;
			}
			@include max-screen($sm-min) {
				display: none;
			}
		}
		.img_mob {
			display: none;
			@include max-screen($sm-min) {
				display: inherit;
				width: 100%;
				max-width: 100%;
				text-align: center;
				clear: both;
			}
		}
		.txt_center {
			display: none;
		}
		.txt_left {
			float: left;
			margin-left: 360px;
			clear: both;
			@include max-screen($lg-min) {
				margin-left: 20px;
			}
		}
		.txt_right {
			float: right;
			margin-right: 360px;
			clear: both;
			@include max-screen($lg-min) {
				margin-right: 20px;
			}
		}
		.txt {
			max-width: 600px;
			padding: 15px;
			h5 {
				margin: 0;
				padding: 0;
				margin-bottom: 10px;
				font-weight: bold;
				font-size: 30px;
				@include max-screen($xxxs-max) {font-size: 22px;}
			}
			p {
				margin: 0;
				color: black;
				text-align: justify;
				font-size: 20px;
			}
			@include max-screen($sm-min) {
				display: block;
				max-width: 100%;
				margin: 0 auto;
			}
		}
	}
	/*    (    https://navitel.ru/ru/autoaccss/navitel-sbm) */
	.landing_sbm {
		.block {
			> div {
				max-width: 1000px;
				margin: 0 auto;
				.img {
					width: 38%;
					max-width: 38%;
					margin: 20px;
					text-align: center;
					@include max-screen($sm-min) {
						float: none;
						width: 100%;
						max-width: 100%;
						text-align: center;
						margin: 0;
						padding: 20px;
					}
				}
				.txt {
					max-width: 50%;
					margin: 20px;
					text-align: justify;
					@include max-screen($sm-min) {
						float: none;
						width: 100%;
						max-width: 100%;
						margin: 0;
						padding: 20px !important;
					}
				}
			}
		}
		.block1 {
			min-height: 650px;
			background-size: auto;
			background-repeat: no-repeat;
			background-position: center bottom;
			clear: both;
			> div {
				.img {
					float: left;
				}
				.txt {
					float: right;
					padding-top: 70px;
					h5 {
						font-weight: bold;
						font-size: 21px;
					}
				}
			}
			@include max-screen($sm-min) {
				background-image: none !important;
			}
		}
		.block2 {
			min-height: 250px;
			clear: both;
			> div {
				.img {
					float: right;
				}
				.txt {
					float: left;
					padding-top: 50px;
				}
			}
		}
		.block3 {
			min-height: 150px;
			padding-top: 50px;
			clear: both;
			> div {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: space-between;
				align-items: stretch;
				padding: 0 20px;
				.icon {
					float: left;
					width: 150px;
					text-align: center;
					padding-bottom: 30px;
					img {
						margin-bottom: 10px;
					}
				}
				@include max-screen($sm-min) {
					justify-content: center;
				}
			}
		}
	}
}
/* end   */



#pckg {
  .reveal {
    justify-content: center;
    .icon-text-product {
      padding: 3rem 0 0 0;
    }
  }
  p {
    font-size: 11px;
  }
  .row {
    text-align: justify;
  }
}



#maps {
  h2 {
    margin-bottom: 40px;
  }
  .row {
    text-align: justify;
  }
}



#videos {
  .thumb-wrap {
    position: relative;
    padding-bottom: 56.25%; /*     16:9 ( 4:3   75%) */
    height: 0;
    overflow: hidden;
    @include max-screen(380px) {
      padding-bottom: 63%;
    }
  }
  .thumb-wrap iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-width: 0;
    outline-width: 0;
  }
  .col-xs-12 {
    padding-bottom:14px;
  }
}


#reviews {
  h2 {
    margin-bottom: 40px;
  }
  p {
    span:nth-child(1) {
      position: absolute;
      width: 24px;
      text-align: right;
    }
    a:nth-child(2) {
      margin: 0 0 0 30px;
      display: block;
    }
    span:nth-child(3) {
      display: block;
      margin: 0 0 0 30px;
      font-size: 12px;
    }
  }
}



#docs {
  h2 {
    margin-bottom: 40px;
  }
  .file {
    color: #ffffff;
    font-size: 18px;
    font-weight: $default-font-weight;
    margin-bottom: 30px;
    margin-right: 50px;
    a {
	  color: inherit;
	  @include transition();
      padding-left: 40px;
      position: relative;
      display: block;
	  @include hover() {
	    color: $key-color-1;
	    @include transition();
	  }
    }
    i {
      position: absolute;
      left: 0;
      top: 2px;
    }
    &:last-child {
	  margin-right: 0;
    }
  }
}