.link__button {
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 500;
  position: relative;
  color: #379D34;
  margin-left: 25px;

  &--login:before {
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
    background-image: url(/images/menu/login.svg);
    left: -45px;
    top: -11px;

    @include max-screen($md-max) {
        top: inherit;
    }
  }

  &--register::before {
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
    background-image: url(/images/menu/reg.svg);
    left: -45px;
    top: -11px;

    @include max-screen($md-max) {
        top: inherit;
    }
  }
}

.link__button:nth-child(2) {
  margin-left: 55px;
}