.partners {

  &__item {
    margin-bottom: 40px;
  }

  &__image-box {
    @extend .flex, .flex--center, .flex--center-y;
    width: 170px;
    height: 170px;
    padding: 10px;
    border: 1px solid $border-color;

    @include max-screen($sm-max) {
      margin-bottom: 40px;
      width: 100%;
    }


    img {
      object-fit: scale-down;
    }
  }

  &__title {
    font-weight: $default-font-weight;
    color: $blue-color;
    font-size: 18px;

    h1,h2,h3,h4,h5,h6 {
      font-size: inherit;
      color: inherit;
    }
  }
}