.news {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 40px;
  border: 1px solid$border-color;
  width: 100%;
  margin-bottom: 30px;
  @include transition();

  &:before {
    position: absolute;
    content: "";
    top:0;
    left:0;
    width: 100%;
    height: 2px;
    background-color: $key-color-1;
    opacity: 0;
    visibility: hidden;
  }

  @include hover() {

    &:before {
      opacity: 1;
      visibility: visible;
      @include transition();
    }
    box-shadow: 5px 11px 20px 7px hsla(0,0%,41%,.1);
    @include transition();
  }


  &__date {
    font-size: 14px;
    color: $key-color-1;
    margin-bottom: 30px;
  }

  &__text {
    color: $heading-color;
    font-weight: $default-font-weight-light;
    font-size: 21px;
    margin-bottom: 30px;
    @include transition();

    @include hover() {
      color: $key-color-1;


      u {
        border-bottom: 1px solid $key-color-1;
      }
      @include transition();
    }

    u {
      text-decoration: none;
      border-bottom: 1px solid $heading-color;
    }
  }

  &__social {

    .ico-facebook {
      color: #43609c;
      margin-right: 10px;
      @include transition();

      @include hover() {
        color: lighten(#43609c, 20%);
        @include transition();
      }
    }
    .ico-twitter {
      color: #1da1f2;
      margin-right: 10px;
      @include transition();

      @include hover() {
        color: lighten(#1da1f2, 20%);
        @include transition();
      }
    }
    .ico-vk {
      color: #45668e;
      @include transition();

      @include hover() {
        color: lighten(#45668e, 20%);
        @include transition();
      }
    }
  }

}



.country-box {
  padding: 30px 0;
  border-bottom: 1px solid $border-color;
  font-size: 18px;
  font-weight: $default-font-weight-light;
  color: $heading-color;

  &__title {
    font-size: $default-accordion-font-size;
    text-transform: uppercase;
    font-weight: $default-font-weight;
    margin-bottom: 20px;
  }

  &__text {
    margin-bottom: 15px;

    a {
      font-weight: inherit;
      font-size: inherit;
      @include transition();

      @include hover() {
        color: $key-color-1;
        @include transition();
      }
    }
  }

  &__small-title {
    font-size: $default-lead-font-size;
    font-weight: $default-font-weight;
    text-transform: lowercase;
    margin-bottom: 15px;
    letter-spacing: 1px;
    &::first-letter {
      text-transform: uppercase;
    }
  }
}

.country-box:first-child {
  border-top: 1px solid $border-color;
}

@media screen and (max-width: 1199px) {
  .newsSetNav {
    height: auto;

    .nav__list {
      padding: 0;
      padding-bottom: 3.125rem;
    }

    .nav__item {
      display: inline-block;
      padding: 0 10px;

      a {
        color: #333;
      }
    }
  }
}