.devices-table {
  margin-bottom: 20px !important;
  font-weight: $default-font-weight;
  font-size: 14px;
  color: $heading-color;


  tr td,
  tr th {
    padding: 22px 25px;


    @include max-screen($xs-max) {
      padding: 12px 15px;
    }
  }

  @include max-screen($xxs-max) {
    font-size: 14px;
  }

  tr th {
    border: 1px solid transparent;
    //border-bottom: 3px solid $key-color-1;
    background-color: $key-color-1;
    color: #ffffff;
    font-size: 14px;
    font-weight: $default-font-weight-medium;
  }

  tr:nth-child(odd) {
    background-color: #fafafa;
  }

  tr td:first-child {
    //max-width: 120px;
    min-width: 280px;

    @include max-screen($md-max) {
      min-width: 0;
    }
  }


  td {
    border: 1px solid $border-color;
    vertical-align: top;

    a {
      color: #4089f6;
      @include transition();

      @include hover() {
        color: $key-color-1 !important;
        @include transition();
      }
    }
  }


}


