.table-custom {
  margin-top: 24px;
  margin-bottom: 24px;
  border-top: 1px solid #e5e5e5;
  border-left: 1px solid #e5e5e5;
  font-size: 16px;
}

.table-custom-head {
  background: #f6f6f6;
  display: -webkit-flex;
  display: flex;
  > div {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 15px 25px;
    line-height: 1.5;
    border-right: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    font-size: 93%;
    &:nth-child(1) {
      width: 43.4%;
      font-size: 100%;
      font-weight: 500;
    }
    &:nth-child(2) {
      width: 19%;
    }
    &:nth-child(3) {
      width: 37.6%;
    }
  }
}

.table-custom-row {
  display: -webkit-flex;
  display: flex;
  > div {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 15px 25px;
    line-height: 1.5;
    border-right: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    font-size: 93%;
    &:nth-child(1) {
      width: 43.4%;
    }
    &:nth-child(2) {
      width: 19%;
      font-size: 100%;
    }
    &:nth-child(3) {
      width: 37.6%;
    }
    > div:nth-child(1) {
      display: none;
    }
  }
}

@media screen and (max-width: 767px) {
  .table-custom {
    border: none;
  }
  .table-custom-head {
    display: none;
  }
  .table-custom-row {
    display: block;
    margin: 0 0 10px 0;
    border-top: 1px solid #e5e5e5;
    border-left: 1px solid #e5e5e5;
    > div {
      display: block;
      width: 100% !important;
      &:nth-child(1) {
        background: #f6f6f6;
        > div:nth-child(2) {
          font-size: 100%;
          font-weight: 500;
        }
      }
      &:nth-child(2), &:nth-child(3) {
        font-size: 90%;
        > div {
          &:nth-child(1) {
            display: inline-block;
            width: 49%;
            font-weight: 500;
          }
          &:nth-child(2) {
            display: inline-block;
            width: 49%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .table-custom-row > div {
    &:nth-child(2) > div {
      &:nth-child(1), &:nth-child(2) {
        width: 100%;
      }
    }
    &:nth-child(3) > div {
      &:nth-child(1), &:nth-child(2) {
        width: 100%;
      }
    }
  }
}



.block_with_border {
  border: 1px solid #f1f1f1;
}

.payment_note {
  font-size: 93%;
  padding-left: 25px;
  padding-top: 26px;
}

.payment_info_line {
  display: -webkit-flex;
  display: flex;
  border-top: 1px solid #e5e5e5;
  border-left: 1px solid #e5e5e5;
  font-size: 93%;
  margin: 0 0 25px 0;
  > div {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 20px;
    border-right: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    &:nth-child(1) {
      width: 110px;
      text-align: center;
    }
    &:nth-child(2) {
      width: calc(100% - 110px);
    }
    img {
      width: 100%;
      max-width: 70px;
      height: auto;
    }
  }
}

.payment_info_line_multi_img > div:nth-child(1) {
  flex-direction: column;
}

@media screen and (max-width: 480px) {
  .payment_info_line {
    display: block;
    > div {
      display: block;
      &:nth-child(1), &:nth-child(2) {
        width: 100%;
      }
    }
  }
}

.payment_requisites_wrap {
  padding: 20px;
}

.payment_requisites_line {
  display: -webkit-flex;
  display: flex;
  font-size: 93%;
  > div {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 20px;
    border: 1px solid #e5e5e5;
    padding: 7px 14px;
    margin: 0 2px 2px 0;
    font-size: 93%;
    &:nth-child(1) {
      width: 30%;
    }
    &:nth-child(2) {
      width: 70%;
    }
  }
}

@media screen and (max-width: 767px) {
  .payment_requisites_line {
    display: block;
    border-bottom: 1px solid #e5e5e5;
    margin: 2px 0;
    > div {
      display: block;
      margin: 0;
      border-bottom: none;
      &:nth-child(1) {
        width: 100%;
        font-weight: 500;
      }
      &:nth-child(2) {
        width: 100%;
        border-top: none;
        padding-top: 0;
      }
    }
  }
}