.nav {
    font-size: $default-font-size;

    @include max-screen($md-max) {
        display: table;
        width: 100%;
        height: 100%;
        text-align: center;
        vertical-align: middle;
        margin: $clear-margin;
        padding: $clear-padding;
    }

    @include max-screen($md-max) {
        font-size: 24px;
    }

    @include max-screen($sm-max) {

    }

    @include max-screen($xs-max) {

    }

    @include max-screen($xxs-max) {
        font-size: 18px;
    }

    &__list {
        @extend .flex, .flex--center-y;

        @include max-screen($md-max) {
            margin: $clear-margin;
            padding: 50px 20%;
            text-align: center;
            display: table-cell;
            text-align: center;
            vertical-align: middle;
            width: 100%;
            height: 100%;

            @include max-screen($xs-max) {
                padding-left: 100px;
                padding-right: 100px;
            }

            @include max-screen($xxs-max) {
                padding-left: 65px;
                padding-right: 65px;
            }
        }

        &--login {
            height: 30%;
            position: absolute;
            display: none;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            bottom: 0;
            background-color: $key-color-3;
            left: 0;
            padding: 0;

            @include max-screen($md-max) {
                display: flex !important;
            }
        }
    }

    &__item {
        position: relative;
        margin-right: 30px;

        @include max-screen($md-max) {
            margin: $clear-margin;
        }

        &:last-child {
            margin-right: 0;
        }

        > a {
            display: block;
            text-align: center;
            text-transform: uppercase;
            font-size: 13px;
            font-weight: $default-font-weight-medium;
            color: $key-color-3;
            padding: 10px 0;

            @include max-screen($md-max) {
                color: #fff;
                font-size: 18px;
            }

            @include max-screen($xxs-max) {
                padding-top: 10px;
                padding-bottom: 10px;
            }

            @include hover() {
                color: $key-color-1;

                @include max-screen($md-max) {
                    color: inherit;
                }
            }

            &.is-active {
                font-weight: 700;
                color: $key-color-1;

                @include max-screen($md-max) {
                    color: #000000;
                }
            }
        }

        > a:hover {
            color: $key-color-1;

            @include max-screen($md-max) {
                color: #000000;
            }
        }
    }
}


.site-nav {
    width: 100%;
    font-size: 13px;
    padding-top: 20px;

    &__list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        @include max-screen($sm-max) {
            overflow-x: auto;
            justify-content: space-between;
        }

        &--around {
            justify-content: space-around;

            @include max-screen($sm-max) {
                justify-content: space-between;
            }
        }


        &.is-scroll {
            &::before {
                @include pseudo;
                top: 0;
                right: 0;
                bottom: 0;
                width: 60px;
                background: linear-gradient(to right, rgba(250,250,250,0) 0%,rgba(250,250,250,1) 100%);
                z-index: 1;
                pointer-events: none;
                -webkit-touch-callout: none;
                user-select: none;
            }
        }

        &--toRight {
            justify-content: flex-end;
        }
    }

    &__item {
        position: relative;

        min-width: 150px;
        text-transform: uppercase;
        letter-spacing: 2px;
        @include transition();


        a {
            padding: 10px 0;
            text-align: center;
            font-size: 13px;
            word-wrap: break-word;
            color: $default-font-color;
            width: 100%;
            display: flex;
            justify-content: center;
            @include transition();

            &:before {
                content: "";
                position: absolute;
                bottom: 0;
                width: 100%;
                height: 3px;
                background: $key-color-1;
                opacity: 0;
                visibility: hidden;
            }

            @include hover() {
                color: $heading-color !important;
                font-weight: $default-font-weight-medium;
                @include transition();
                &:before {
                    opacity: 1;
                    visibility: visible;
                    @include transition();
                }
            }

            @include max-screen($xs-max) {
                font-size: 10px;
            }

            @include max-screen($xxs-max) {
                padding: 15px 0;
            }
        }


        &.is-active a {

            color: $heading-color !important;
            font-weight: $default-font-weight-medium !important;

            &:before {
                opacity: 1;
                visibility: visible;
                @include transition();
            }
        }

        span {
            font-weight: $default-font-weight-medium;
            font-size: 15px;
            text-transform: none;
            color: #1da04a;
        }

        @include max-screen($xxs-max) {
            min-width: 120px;
        }


        &--floating {
            position: absolute;
            left: 50%;
            @include translate(-50%,0);
        }

        &_video {
            min-width: 80px;
        }
        &_reviews {
            min-width: 100px;
        }
    }

    &__title {
        font-family: $default-font-family;
        font-size: 24px;
        font-weight: $default-font-weight-light;
        color: $heading-color;
        margin-bottom: 35px;

        h1,h2,h3,h4,h5,h6 {
            color: inherit;
            font-size: inherit;
            font-weight: inherit;
            margin: 0;
            padding: 0;
        }

        &--noMargin {
            margin-bottom: 0;
        }
    }



    &__icon-text {
        padding: 100px 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include max-screen($sm-max) {
            padding: 50px 0;
        }

        @include max-screen($xxs-max) {
            padding: 20px;
        }


        &__green-icon {
            margin-bottom: 30px;
            .ico {
                font-size: 50px;
                color: $key-color-1;
            }
        }

        &__text {
            padding: 0 30px;
            font-size: 16px;
            text-align: center;
            color: $heading-color;
            font-weight: $default-font-weight;
        }
    }


    &__date {
        font-size: 14px;
        color: $key-color-1;
        font-weight: $default-font-weight;
        margin-top: 10px;
    }


}