-android.img-text {
    $this: &;
    
    @extend .flex, .flex--column;
    overflow: hidden;
    
    &__item {
        @extend .flex, .flex--center-y, .flex--nowrap, .flex--space-between;
        flex: 0 0 auto;
        width: 100%;
        margin-bottom: 80px;
        
        @include max-screen($md-max) {
            margin-bottom: 50px;    
        }
        
        @include max-screen($sm-max) {
            flex-wrap: wrap;
        }
        
        @include max-screen($xs-max) {
            margin-bottom: 30px;
        }

        &--image_text {
            #{$this}__image {
                @include max-screen($sm-max) {
                    margin-bottom: 25px;    
                }
            }

            #{$this}__text {
                padding-left: 50px;
                
                @include max-screen($md-max) {
                    padding-left: 30px;
                }
                
                @include max-screen($sm-max) {
                    padding-left: 0;
                }
            }
            
            @include max-screen($sm-max) {
                flex-direction: column;
            }
        }

        &--text_image {
            #{$this}__text {
                text-align: right;
                padding-right: 50px;
                
                @include max-screen($md-max) {
                    padding-right: 30px;
                }
                
                @include max-screen($sm-max) {
                    text-align: left;
                    padding-right: 0;
                }
            }
            
            #{$this}__image {
                @include max-screen($sm-max) {
                    margin-bottom: 25px;    
                }
            }

            @include max-screen($sm-max) {
                #{$this}__text {
                    
                }

                #{$this}__image {
                    order: -1;
                }
            }
            
            @include max-screen($sm-max) {
                flex-direction: column;
            }
        }

        &--text {
            #{$this}__text {
                flex: 0 1 100%;

                &-inner {
                    
                }
            }
        }

        &--text_text {
            #{$this}__text {
                padding-right: 50px;
                
                @include max-screen($md-max) {
                    padding-right: 30px;
                }
                
                @include max-screen($sm-max) {
                    padding-right: 0;    
                }

                &:last-child {
                    padding-right: 0;
                    padding-left: 50px;
                    
                    @include max-screen($md-max) {
                        padding-left: 30px;
                    }
                    
                    @include max-screen($sm-max) {
                        padding-left: 0;
                        margin-top: 30px;
                    }
                }

                &-inner {
                    
                }
            }
        }

        &--image {
            #{$this}__image {
                flex: 0 1 100%;
            }
        }

        &--image_image {
            #{$this}__image {
                &:last-child {
                    @include max-screen($sm-max) {
                        margin-top: 30px;
                    }
                }
            }
            
            @include max-screen($sm-max) {
                flex-direction: column;
            }
        }
    }
    
    &__image {
        align-self: flex-start;
        text-align: center;
        flex: 0 1 48.6%;

        @include max-screen($sm-max) {
            flex: 0 1 100%;
            width: 100%;
        }
        
        img {
            width: 100%;
            border-radius: $default-radius;
        }

        &--center {
            align-self: center;
        }
    }
    
    &__text {
        @extend .flex, .flex--center-y, .flex--center-x;
        flex: 0 1 48.6%;
        margin-left: 0;
        //padding: 30px 15px 5px;
        font-size: 15px;
        color: $heading-color;
        font-weight: $default-font-weight-light;
        text-align: left;
        
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            font-size: 18px;
            color: $heading-color;
            margin: $heading-margin;
            font-weight: $default-font-weight;
            
            @include max-screen($xxs-max) {
                font-size: 16px;
            }

        }
        
        @include max-screen($sm-max) {
            flex: 0 1 100%;
        }

        &-inner {
            max-width: 100%;
            
            > *:last-child {
                margin-bottom: 0;
            }
        }
    }
}