.instruction__main {
  webkit-box-shadow: 0px 0px 25px -10px #000;
  moz-box-shadow: 0px 0px 25px -10px #000;
  o-box-shadow: 0px 0px 25px -10px #000;
  box-shadow: 0px 0px 25px -10px #000;
  padding-left: 3%;
  padding-right: 3%;
  padding-top: 150px;
  padding-bottom: 3%;
}

.instruction__title-first-row {
  font-size: 3em;
  color: grey;
  text-transform: uppercase;
  border-bottom: 2px solid grey;
  display: inline-block;
  padding-left: 10%;
}

.instruction__title-second-row {
  font-size: 2em;
  color: grey;
  padding-left: 10%;
}

.instruction__logo {
  padding-left: 10%;
}

.instruction__title {
  margin-bottom: 200px;
  margin-top: 200px;
}

.instruction__copyrights {
  color: grey;
}

.instruction__content-title {
  font-size: 2.5em;
  padding-top: 100px;
}

.instruction__content-second-level {
  padding-left: 2em;
}

.instruction__content-third-level {
  padding-left: 4em;
}

.instruction__content-fourth-level {
  padding-left: 6em;
}

.instruction__note {
  color: grey;
  font-style: italic;
}

.instruction__note-green {
  color: grey;
  font-style: italic;
  border-left: 3px solid #3d9650;
  display: block;
  padding-left: 0.5em;
}

.instruction__content-second-level + div .instruction__content-third-level + div {
  padding-bottom: 1%;
}

.instruction__content {
  .accordion-btn--alt.accordion-btn--empty:hover .accordion-btn__title > span {
    color: #2d2c2c !important;
  }
  .accordion-btn--empty {
    cursor: default;
  }
}

@media screen and (max-width: 575px) {
  .instruction__content-second-level {
    padding-left: 0.5em;
  }
  .instruction__content-third-level {
    padding-left: 1em;
  }
  .instruction__content-fourth-level {
    padding-left: 1.5em;
  }
  .instruction__title-first-row {
    font-size: 2em;
  }
  .instruction__content .accordion-btn {
    > {
      .ico, .ico-default {
        font-size: 0.575rem;
      }
    }
    &.is-active > {
      .ico, .ico-default {
        font-size: 0.575rem;
      }
    }
  }
}

.instruction__tbl {
  background: #f7f6e3;
  margin: 0;
  .title {
    background: #E2F8DF;
  }
  .col-12 {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 10px 15px;
    text-align: left;
    span {
      font-size: 12px;
      font-size: 13px;
      font-weight: normal;
      color: #9e9e9e;
    }
  }
  .d-none {
    display: none !important;
  }
  .d-block {
    display: block !important;
  }
  .color_red {
    color: #c80909 !important;
  }
  .color_green {
    color: #2b950e !important;
  }
}

@media screen and (max-width: 767px) {
  .instruction__tbl {
    border-bottom: 1px solid #ccc;
    .mob-title {
      font-weight: bold;
      border-top: 1px solid #ccc;
      padding-bottom: 0 !important;
    }
    .col-12 {
      display: block;
    }
    .title_ins {
      border-top: 1px solid #ccc;
      padding: 15px;
      font-weight: bold;
      background: #E2F8DF;
      margin: 0 0 -10px 0;
    }
  }
}

@media screen and (min-width: 768px) {
  .instruction__tbl {
    .d-sm_block {
      display: block !important;
    }
    .d-sm-none {
      display: none !important;
    }
  }
}

.instruction__pravo_tbl {
  .col-level-2 {
    padding: 10px 0;
  }
  .col-12 > .row {
    width: 100%;
    margin: 0;
    > .col-12 {
      justify-content: center;
    }
  }
  .mob-title {
    justify-content: left !important;
  }
  .title_ins {
    background: #E2F8DF;
  }
  .text-center {
    text-align: center;
  }
}

.instruction__track_tbl .col-12 {
  span {
    width: 100%;
    padding: 10px 15px;
    margin: 0;
    border: none;
  }
  text-align: center;
  justify-content: center;
}

@media screen and (max-width: 767px) {
  .instruction__track_tbl {
    .col-12 {
      text-align: left;
      justify-content: left;
    }
    > .col-12 {
      > .row > .col-12 {
        > .row > .col-sm-6 {
          width: 50% !important;
          word-break: break-all;
        }
        padding-top: 0;
        padding-bottom: 0;
      }
      padding-top: 0;
      padding-bottom: 0;
    }
    .col-one-row {
      width: 50% !important;
      word-break: break-all;
      padding: 10px 15px;
    }
    .title_ins {
      width: 100%;
      margin: 0;
      border: none;
      font-weight: normal;
      font-size: 13px;
      padding: 10px 15px;
      color: #9e9e9e;
    }
  }
  .instruction__tbl > .d-block {
    width: 100%;
    font-size: 13px;
    padding: 10px 15px;
    color: #9e9e9e;
  }
}

@media screen and (max-width: 500px) {
  .instruction__track_tbl {
    > .col-12 > .row > .col-12 > .row > .col-sm-6, .col-one-row {
      width: 100% !important;
    }
  }
}

.instruction__manualka ol {
  list-style: none;
  counter-reset: li;
}
.instruction__manualka li:before {
  counter-increment: li;
  content: counters(li,".") ". ";
}