.color-key-1 {
    color: $key-color-1 !important;
}

.color-key-2 {
    color: $key-color-2 !important;
}

.bg-color-key-1 {
    background-color: $key-color-1 !important;
}

.bg-color-key-2 {
    background-color: $key-color-2 !important;
}

.lead {
	font-size: $default-lead-font-size;
    font-weight: $default-lead-font-weight;
}

.section {
	padding: 90px 0;

  @include max-screen($xxs-max) {
    padding: 40px 0;
  }

  &--border-top {
    border-top: 1px solid $border-color;
  }

  &--buy {
    background-color: $key-color-1;
    padding: 30px 0;
    text-align: center;
  }

  &--product {
    padding: 200px 0 100px 0;

    @include max-screen($xxs-max) {
      padding: 100px 0 40px 0;
    }
  }

  &--boxes {
    padding: 0;
  }

  &--map {
    background-size: contain;
    background-position: 0 20%;
    background-repeat: no-repeat;
  }

  &--slider {
    border-top: 1px solid $border-color;
  }

  &--newsletter {
    border-top: 1px solid $border-color;
  }

  &--faq {
    padding: 150px 0 100px 0;
    background-repeat: no-repeat;
    //background-size: contain;
    background-position: top right;

    @include max-screen($xxs-max) {
      padding: 120px 0 40px 0;
    }
  }

  &--log {
    position: fixed;
    width: 100%;
    z-index: 99;
    background-color: #f3faf6;
    padding: 0;
    margin: 0;
    @include transition();

    @include max-screen($xxs-max) {
      > .container {
        padding-right: 0 !important;
        padding-left: 0 !important;
      }
    }

    .is-top-minimize & {
      margin-top: -18px;
      .site-nav__item a {
        padding: 15px 0;
        @include transition();
      }

      @include max-screen($md-max) {
        margin-top: 0;
      }


      @include max-screen($xxs-max) {
        margin-top: -7px;
      }

    }

    .is-top-shadow & {
      box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1);
    }
  }

  &--nav {
    position: fixed;
    width: 100%;
    z-index: 99;
    background-color: #fbfbfb;
    padding: 0;
    margin: 0;
    @include transition();

    @include max-screen($xxs-max) {
      > .container {
        padding-right: 0 !important;
        padding-left: 0 !important;
      }
    }

    .is-top-minimize & {
      margin-top: -18px;
      .site-nav__item a {
        padding: 15px 0;
        @include transition();
      }

      @include max-screen($md-max) {
        margin-top: 0;
      }


      @include max-screen($xxs-max) {
        margin-top: -7px;
      }

    }

    .is-top-shadow & {
      box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1);
    }
  }

  &--grey {
    background-color: #fbfbfb;
  }

  &--dark {
    background-color: #2d2c2c;
    color: #ffffff !important;
    font-size: 14px;
    font-weight: $default-font-weight;

    .main__title {
      color: inherit;
    }
  }

  &--background {
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
  }
}


.row {

  &--compare {
    border-bottom: 1px solid $border-color;
    padding-bottom: 50px;
  }
}



.main {
  //border-bottom: 1px solid $border-color;

  &__header {
    font-size: 24px;
    font-weight: $default-font-weight-semilight;
    color: $default-font-color;
    //margin-bottom: 40px;
    text-align: center;

    h1,h2,h3,h4,h5,h6 {
      font-size: inherit;
      font-weight: inherit;
      color: inherit;
      margin: 0;
    }
  }

  &__title {
    font-size: 48px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    text-align: center;
    padding-bottom: 30px;

    @include max-screen($xxs-max) {
      font-size: 26px;
    }

    h1,h2,h3,h4,h5,h6 {
      font-size: inherit;
      font-weight: inherit;
      color: inherit;
      margin: 0;
    }
  }

  &__subtitle {
    margin-bottom: 2.5rem;
    text-align: center;
    color: #2d2c2c;
    font-size: 26px;
    font-weight: 300;
    @include max-screen($xxs-max) {
      font-size: 18px;
    }
  }
}

.catalog_card {
  .main {
    &__title {
      padding: 0;
    }
  }
}


.useful-links {
  //display: flex;
  //flex-direction: row;
  //flex-wrap: wrap;
  //align-items: center;
  //justify-content: flex-start;

  @include max-screen($xxs-max) {
    justify-content: center;
  }
}

.useful-link {
  width: 100%;
  height: auto;
  min-height: 225px;
  background-color: transparent;
  border: 1px solid $border-color;
  padding: 50px;
  display: block;
  margin-right: 10px;
  margin-bottom: 10px;
  @include max-screen($sm-min) {
    min-height: auto;
  }

  &__title {
    font-size: 21px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    @include transition();

    > span.green {
      font-weight: $default-font-weight-medium;
      color: $key-color-1;
    }
    @include min-screen($md-min) {
      font-size: 21px;
    }

    @include max-screen($sm-max) {
      font-size: 16px;
    }

    @include max-screen($sm-min) {
      font-size: 21px;
    }
  }

  @include hover() {
    @include transition();
    background-color: $key-color-1;
    color: #ffffff !important;


    .useful-link__title {
      @include transition();
      color: #ffffff !important;
      > span.green {
        color: #ffffff !important;
        @include transition();
      }
    }
  }
}

html[lang="pl"] .useful-link {
  min-height: 260px;
  @include max-screen($sm-min) {
    min-height: auto;
  }
}



.error_image {
  text-align: center;
  img {
    width: 100%;
    max-width: 55px;
    height: auto;
  }
}

@media screen and (max-width: 767px) {
  .error {
    margin: 40px 0 0 0;
  }
}