.footer {
	background-color: #ffffff;
	color: $default-font-color;
	font-size: 14px;
	border-top: 1px solid $border-color;
	//padding: 35px 0;

	a {
		color: inherit;
	}

	a:hover {
		color: $key-color-1;
	}
	
	p {
		margin-bottom: 0;
	}

	&__top {
		padding: 80px 0;
		@include max-screen($xs-max) {
			text-align: center;
		}
		@include max-screen($xxs-max) {
			padding: 40px 0;
		}
	}

	&__column-wrapper {
		@include max-screen($xs-max) {
			margin-top: 50px;
		}


		&--last {
			@include max-screen($sm-max) {
				margin-top: 50px;
				text-align: center;
			}
		}
	}



	&__address {
		margin-bottom: 20px;
	}

	&__phone, &__email {
		margin-bottom: 0;

		a {
			@include transition();
		}

		a:hover{
			padding-left: 10px;
			@include transition();
		}
	}

	&__email {
		margin-top: 20px;
	}

	&__copy {
		//margin-right: 30px;
		padding: 3px 0;
	}
	
	&__create {
		padding: 3px 0;
	}

	&__social {
		margin-top: 30px;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;

		@include max-screen($sm-max) {
			justify-content: center;
		}

		&-item {
			width: 42px;
			height: 42px;
			display: flex;
			justify-content: center;
			align-items: center;
			color: #ffffff;
			margin-right: 10px;
			@include transition();
		}
		&-item:last-child {
			margin: 0;
		}
		&-item:hover {
			text-decoration: none;
		}
		&-item:visited {
			text-decoration: none;
		}
	}


	&__title {
		font-size: 13px;
		font-weight: $default-font-weight;
		letter-spacing: 2px;
		text-transform: uppercase;
		margin-bottom: 20px;

		h1,h2,h3,h4,h5,h6 {
			font-size: inherit;
			font-weight: inherit;
			color: inherit;
			margin: 0;
		}

	}

	&__link {
		margin-bottom: 10px;
	}


	&__bottom {
		padding: 35px 0;
		font-size: 13px;
		border-top: 1px solid $border-color;

		&-link {
			padding: 0 15px;
			margin-bottom: 10px !important;
		}
	}


}