.top {
	background-color: #fff;
	padding: 22px 0;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 500;
	box-shadow: 0 2px 15px rgba(0, 0, 0, 0);
	@include transition();

	@include max-screen($md-max) {
		padding-top: 15px !important;
		padding-bottom: 15px !important;
	}

	&--borderred {
		border-bottom: 1px solid $border-color;
	}

	.is-top-shadow & {
		box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1);
	}

	.is-top-minimize & {
		padding: 15px 0;

		&__logo {
			transform: translate(0, 0) scale(0.7, 0.7);
			top: -40px;
			transition: all .3s ease-in-out;
			@include transition();

			@include max-screen($md-max) {
				transform: translate(0, 0) scale(1, 1);
				@include transition();
			}
		}
	}

	&__logo {
		transform: translate(0, 0) scale(1, 1);
		transition: all .3s ease-in-out;
		@include transition();

		//@include max-screen($md-max) {
		//	max-width: 185px !important;
		//	@include transition();
		//}
        //
		//@include max-screen($sm-max) {
		//	max-width: 165px !important;
		//	@include transition();
		//}
        //
		@include max-screen($xxs-max) {
			max-width: 115px !important;
			@include transition();
		}
		@include max-screen($xxxs-max) {
			width: 36px;
			overflow: hidden;
		}

		a {
			@include hover() {
				img {
					opacity: 0.5;
				}
			}
		}

		img {
			@include transition();
			width: 210px;

			@include max-screen($xxxs-max) {
				max-width: none;
				width: 115px;
			}
		}
	}

	&__nav {
		//margin-right: auto;
		//margin-left: 0;
		@include max-screen($md-max) {
			display: block !important;
			background-color: rgba($key-color-1, .98);
			margin: $clear-margin;
			padding: $clear-padding;
			opacity: 0;
			visibility: hidden;
			position: fixed;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 900;
			@include transition();

			.is-menu-open & {
				overflow-y: auto;
				opacity: 1;
				visibility: visible;
			}
		}
	}

	&__button {
		margin-left: 0;
		margin-right: 0;

		display: flex;
		align-items: center;

		i {
			margin-right: 10px;
		}

		@include max-screen($md-max) {
			margin-right: 20px;
		}

		@include max-screen($xs-max) {

			.btn {
				padding: {
					top: $btn-small-padding-top;
					right: 20px;
					bottom: $btn-small-padding-bottom;
					left: 20px;
				}
				font-size: $btn-small-font-size;
			}
		}

		&-small {
			display: none;

			.btn {
				padding: {
					top: $btn-small-padding-top;
					right: 13px;
					bottom: $btn-small-padding-bottom;
					left: 13px;
				}
				font-size: $btn-small-font-size;
			}

			@include max-screen($xs-max) {
				display: block;
			}

			@include max-screen($xs-max) {
				margin-right: 5px;

			}
		}

		>.btn:first-child {
			margin-right: 5px;
		}

	}



	&__flags {
		margin-left: 30px;
		margin-right: 28px;

		@include max-screen($md-max) {
			margin-left: 30px;
			margin-right: 20px;
		}

		@include max-screen($xxs-max) {
			//display: none;
			margin-left: 5px;
			margin-right: 5px;
		}

		&-nav {
			position: relative;

			&__list {
				@extend .flex, .flex--center-y;
			}

			&__list:hover {
				> .top__flags-nav__submenu {
					opacity: 1 !important;
					visibility: visible !important;
					@include transition();
				}
			}

			&__item {
				position: relative;
				margin-right: 2px;

				@include max-screen($md-max) {

				}

				@include max-screen($xxs-max) {

				}

				#currentLang > img {width: 20px;}

				//&:last-child {
				//	margin-right: 0;
				//}

				> a {
					display: flex;
					text-align: center;
					color: $heading-color;
					padding: 3px;
					align-items: center;
					font-size: 22px;
					justify-content: center;
					border: 1px solid transparent;
					border-radius: 50%;

					span {
						font-size: 13px;
						font-weight: $default-font-weight-medium;
						padding-left: 10px;
						text-transform: uppercase;

						i {
							display: flex;
						}
					}
				}
			}


			&__submenu {
				position: absolute;
				background-color: white;
				border-radius: 10px;
				border: 1px solid $border-color;
				padding: 0;
				top: 35px;
				overflow: hidden;
				left: -22px;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				opacity: 0;
				visibility: hidden;
				@include transition();

				&-item a {
					display: flex;
					flex-direction: row;
					align-items: center;
					padding: 6px 40px 6px 20px;
					color: $heading-color;
					text-transform: uppercase;

					span {
						font-size: 13px;
						font-weight: $default-font-weight-medium;
						padding-left: 10px;
						display: inline-block;
						width: 30px;
					}

					img {
						width: 20px;
						height: 20px;
						max-width: none;
					}

				}

				&-item a:hover {
					background-color: $border-color;
				}
			}
		}
	}

	&__person {
		margin-right: 15px;
		display: flex;
		align-items: center;
		position: relative;

		a {
			color: $default-font-color;
		}

		a:hover {
			color: $key-color-1;
		}

		.logout {
			position: absolute;
			top:1px;
			right:-20px;
			@include max-screen($xxs-max) {
				right:0px;
			}
		}

		.icon {
			display: none;
			vertical-align: middle;
			font-size: 20px;
			margin-right:25px;
			margin-left: 15px;
			@include max-screen($xxs-max) {
				display: block;
			}
		}


		.text {
			@include max-screen($xxs-max) {
				display: none;
			}

		}

	}

	&__wrapper {
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}

}


.shadow {
	box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1);
}
