.library-row {
  width: 100%;
  border: 1px solid $border-color;
  margin-bottom: 10px;


  &__device > div {
    margin-top: 10px;
  }

  &__image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100%;
    width: 100%;

    @include max-screen($xs-max) {
      min-height: 200px;
    }

    &--logo {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #f9f9f9;

      > img {
        object-fit: scale-down;
      }

      @include max-screen($xs-max) {
        min-height: 200px;
      }
    }
  }

  &__box-wrapper {
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__title {
    font-size: 24px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    margin-bottom: 30px;
  }

  &__text {
    text-align: center;
    font-size: 18px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    margin-bottom: 30px;
  }

  &__button {

  }

  &__screen {
    .img {
      margin: 15px 0;
    }
  }

  &__logo {
    padding:40px 0;
    a {
      display:inline-block;
      border-top: 1px solid #000000;
      color: #000000;
      font-weight: bold;
    }

    p {
      font-size: 18px;
      font-weight: $default-font-weight-light;
      color: $heading-color;
      margin-bottom: 20px;
    }

    .img {
      margin: $default-margin;
      text-align: center;

      img {
        @include max-screen($xs-min) {
          width: auto !important;
        }
      }

      &--left {
        float: left;
        margin: 0 30px 15px 0 !important;
      }

      &--left {
        max-width: 45%;
        img {
          display: block;
          width: 100%;

          @include max-screen($xs-min) {
            display: inline-block !important;
            width: auto !important;
          }
        }

        @include max-screen($xs-min) {
          max-width: 100% !important;
          float: none !important;
          margin: 0 0 25px !important;
          text-align: center !important;
        }
      }
    }
  }
}


.library-presentation-item {
  width: 100%;
  border: 1px solid $border-color;
  display: block;
  @include transition();
  min-height: 371px;
  margin-top: 20px;

  &:hover {
    .library-presentation-item__upper .arrow {
      right: 40px;
      @include transition();
    }
  }

  @include max-screen($sm-max) {
    margin-bottom: 10px;
  }


  &__upper {
    min-height: 155px;
    position: relative;
    background-color: #f3f3f3;
    padding: 30px 40px;
    font-size: 21px;
    font-weight: $default-font-weight-light;
    color: $heading-color;

    .arrow {
      position: absolute;
      width: 30px;
      right: 30px;
      bottom: 30px;
      content: '';
      color: $heading-color;
      @include transition();
    }
  }

  &__lower {
    padding: 30px 40px;
    font-size: 18px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
  }
}