.compare-row {
  @extend .flex, .flex--center-y;
  margin-top: 20px;
  border-top: 1px solid $border-color;
  border-bottom: 1px solid $border-color;
  padding: 28px 0 27px 0;

  @include max-screen($xs-max) {
    justify-content: center;
  }

  &__wrapper {
    @extend .flex, .flex--center-y;
    margin: 25px 0 25px 0;

    @include max-screen($sm-max) {
      justify-content: center;
    }
  }

  &__boxes {
    @extend .flex, .flex--center-y;
    margin-right: 25px;

    @include max-screen($xxs-max) {
      padding: 20px;
      margin-right: 0;
    }
  }

  &__boxes-item {
    @extend .flex, .flex--center-y, .flex--center;
    width: 52px;
    height: 52px;
    border: 1px solid $border-color;
    padding: 15px 10px;
    border-radius: 5px;
    margin-right: 5px;
    position: relative;
  }

  &__boxes-item-del {
    top: 2px;
    position: absolute;
    right: 2px;
    line-height: 1;
    font-size: 10px;
    cursor: pointer;

    &:hover {color:#3fbf3f;}
  }

  &__open-compare {
    padding: 0 0 0 6px;
    line-height: 1.7;
  }

  &__button {
    background-color: #e5e5e5;
    color: #2d2c2c !important;

    @include hover() {
      background-color: $key-color-2 !important;
      color: #ffffff !important;
    }
  }

  &__right-side {
    @extend .flex, .flex--center-y, .flex--center;
    margin-left: auto;
    margin-right: 0;

    @include max-screen($xs-max) {
      margin-top: 20px;
      margin-left: auto;
      margin-right: auto;
      text-align: center;
    }
  }

  &__label {
    font-size: 16px;
    font-weight: $default-font-weight-light;
    margin-right: 20px;
  }

  &__size-selector {
    @extend .flex, .flex--center-y, .flex--center;
    width: 54px;
    height: 52px;
    border-radius: 26px;
    border: 1px solid $border-color;
    background-color: transparent;
    margin-right: 5px;
    color: $heading-color;
    @include transition();

    @include hover() {
      background-color: $key-color-1;
      color: #ffffff;
      @include transition();
    }

  }

  .is-active {
    background-color: $key-color-1;
    color: #ffffff;
  }
}


.compare-item {
  @extend .flex, .flex--center-y, .flex--center;
  flex-direction: column;
  width: 100%;


  &__image {

  }

  &__name {
    font-size: 21px;
    color: $heading-color;
    margin-top: 30px;

    @include max-screen($xxs-max) {
      font-size: 16px;
    }
  }

  &__delete {
    margin-top: 10px;
    font-size: 14px;
    cursor: pointer;
    @include transition();

    @include hover() {
      color: $color-danger;
      @include transform(rotate(90deg));
      @include transition();
    }

    > i {
      font-size: 11px;
    }
  }
}