@font-face {
  font-family: $project-font-family-icon;
  src:
    url('../fonts/icons.ttf?v=1') format('truetype'),
    url('../fonts/icons.woff?v=1') format('woff'),
    url('../fonts/icons.svg?v=1') format('svg');
  font-weight: normal;
  font-style: normal;
}

.ico {
  font-family: $project-font-family-icon !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



