.private-area-box {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 75px;
  border: 1px solid $border-color;
  width: 100%;
  margin-bottom: 30px;
  overflow: hidden;
  @include transition();

  &:before {
    position: absolute;
    content: "";
    top:0;
    left:0;
    width: 100%;
    height: 2px;
    background-color: $key-color-1;
    opacity: 0;
    visibility: hidden;
  }

  @include hover() {
    @include transition();

    .private-area-box__name {
      color: $key-color-1;
      border-bottom: 1px solid $key-color-1;
      @include transition();
    }

    &:before {
      opacity: 1;
      visibility: visible;
      @include transition();
    }
    box-shadow: 5px 11px 20px 7px hsla(0,0%,41%,.1);

  }


  @include max-screen($xxs-max) {
    padding: 30px;
  }

  &__bg-icon {
    position: absolute;
    bottom: -50px;
    right: -50px;
    opacity: 0.1;

    i {
      font-size: 300px;
    }
  }


  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__icon {
    margin-bottom: 10px;
    i {
      font-size: 60px;
    }
  }


  &__name {
    color: $heading-color;
    font-weight: $default-font-weight-light;
    font-size: 21px;
    //margin-bottom: 30px;
    @include transition();
  }



}