//
// SOCIAL ICONS (as SVG Images)
//

$socico-path: '../img/icons';

// inside .footer a (_footer.scss)
.socico {
    width: 42px;
    height: 42px;
    @include transition();

    &.facebook {
        background-image: url("#{$socico-path}/facebook_1.svg");
        
        @include hover() {
            @include transition();
            background-image: url("#{$socico-path}/facebook_2.svg");
        }
    }

    &.insta {
        background-image: url("#{$socico-path}/insta_1.svg");
        
        @include hover() {
            @include transition();
            background-image: url("#{$socico-path}/insta_2.svg");
        }
    }

    &.ok {
        background-image: url("#{$socico-path}/OK1.svg");
        
        @include hover() {
            @include transition();
            background-image: url("#{$socico-path}/OK2.svg");
        }
    }

    &.telega {
        background-image: url("#{$socico-path}/Telega_1.svg");
        
        @include hover() {
            @include transition();
            background-image: url("#{$socico-path}/Telega_2.svg");
        }
    }

    &.vk {
        background-image: url("#{$socico-path}/VK1.svg");
        
        @include hover() {
            @include transition();
            background-image: url("#{$socico-path}/VK2.svg");
        }
    }

    &.yandex {
        background-image: url("#{$socico-path}/Yandex_1.svg");
        
        @include hover() {
            @include transition();
            background-image: url("#{$socico-path}/Yandex_2.svg");
        }
    }

    &.rutube {
        background-image: url("#{$socico-path}/rutube_1.svg");

        @include hover() {
            @include transition();
            background-image: url("#{$socico-path}/rutube_2.svg");
        }
    }

    &.youtube {
        background-image: url("#{$socico-path}/Youtube_1.svg");
        
        @include hover() {
            @include transition();
            background-image: url("#{$socico-path}/Youtube_2.svg");
        }
    }
}