.updates_downloads {
    margin: 24px 0;
	&__border {
		border: 3px solid #ff5300;
		padding: 10px;
	}
	table {
		margin: 0;
		@include max-screen($xxs-max) {
			table-layout: fixed;
		}
	}
	table tr {
		@include max-screen($xxs-max) {
			display: block;
			padding: 0 0 10px 0;
		}
	}
	table td {
		@include max-screen($xxs-max) {
			display: block;
			width: 100% !important;
		}
		&:not(:last-child) {
			@include max-screen($xxs-max) {
				border-bottom: none;
			}
		}
	}
	img {
		max-width: max-content;
	}
	&__name {
		font-weight: 500;
		padding: 0 0 10px 0;
		color: #333;
	}
	&__file_table tr td:nth-child(1) {
		text-align: center;
	}
	&__file_table tr td:nth-child(2) {
		width: 80%;
		a {
			word-break: break-all;
		}
	}
	&__file_table tr td.wid_100 {
		width: 100%;
	}
	&__file_table tr td:nth-child(3) {
		width: 20%;
		text-align: center;
	}
}