.faq-filters {
  font-family: $default-font-family;
  font-weight: $default-font-weight;
  font-size: 14px;
  padding-right: 60px;


  &__menu {

  }

  &__submenu {
    padding-left: 15px;
    padding-top: 10px;

    &-item a {
      color: $key-color-2;
      font-size: 13px;
    }

    &-item.is-active a{
      color: $key-color-1;
    }
  }

  &__item {
    padding: 10px 0;

    a {
      color: $key-color-2;
      @include transition();

      @include hover() {
        color: $key-color-1;
        @include transition();
      }


    }

    &.is-active > a {
      color: $key-color-1 !important;
      font-weight: $default-font-weight-medium;
    }




  }

  @include max-screen($md-max) {
    position: fixed;
    background-color: #fff;
    left:-600px;
    padding: 20px;
    top: 0;
    //margin-top: 78px;
    box-shadow: 0 2px 15px rgba(0,0,0,.1);
    height: 100%;
    z-index: 900;
    overflow-y: auto;
    width: 240px;
    @include transition();
  }

  @include max-screen($xxs-max) {
    //margin-top: 71px;
    width: 240px;
  }

}


.faq-search {

  label {
    position: absolute;
    top: 11px;
    left: 0;
    font-size: 26px;
    color: $key-color-1 !important;

    >.ico {
      color: inherit;
    }

  }
  input {
    border-top: 1px solid transparent;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    padding-left: 40px;
    background-color: transparent;
  }
  input:focus {
    box-shadow: none;
  }

  input::-webkit-input-placeholder {
    color: $default-font-color;
    opacity: 0.3;
  }
}





.filters-trigger {
  opacity: 0;
  visibility: hidden;
  position: inherit;
  cursor: pointer;
  bottom: 2%;
  right: 2%;
  width: auto;
  height: auto;
  font-size: 18px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  z-index: 8;
  @include transition();

  img {width: 24px;}

  @include max-screen($md-max) {
    opacity: 1;
    visibility: visible;
    @include transition();
  }
}

.filters-trigger-close {
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
  position: absolute;
  left: 250px;
  width: auto;
  height: auto;
  background-color: $default-font-color;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  @include transition();
  z-index: 999;

  svg {
    width: 20px;
    color: #fff;
    fill: #fff;
  }

  .is-open-filters & {
    opacity: 1 !important;
    visibility: visible !important;
    @include transition();
  }
}

.is-filters-active {
  left: 0 !important;
  @include transition();
}

.support-faq {
  background-image: url(/assets/img/faq-bg.png);
  background-repeat: no-repeat;
}
