.c-doc {
	&__row {
		overflow: hidden;
		display: block;
	}
	&__select__box {
		display: inline-block;
		margin: 40px 30px 0px 0;
		position: relative;
		overflow: hidden;
		&:before {
			content: "\e022";
			font-family: "Icons Default";
			position: absolute;
			top: 50%;
			right: 10px;
			transform: translate(0%,-50%);
			color: #818080;
			pointer-events: none;
		}
	}
	&__select.js-pnd-webid,
	&__select.js-dvr-webid,
	&__select.js-tablet-webid,
	&__select.js-combo-webid,
	&__select.js-autoaccs-webid {
		display: none;
	}
	&__select {
		text-overflow: ellipsis;
		white-space: nowrap;
		overflow: hidden;
		cursor: pointer;
		display: block;
		font-family: $form-fields-font-family;
		font-weight: $form-fields-font-weight;
		font-size: $form-fields-font-size;
		padding: $form-fields-padding;
		line-height: inherit;
		&.js-active {
			padding-right: 30px;
			display: block;
		}
	}
	&__description {
		margin: 40px 0;
	}
	&__description__image {
		width: 100%;
		max-width: 195px;
		margin-right: 30px;
		float: left;
	}
	&__description__name {
		color:$key-color-1;
	}
	&__table {
		display: table;
		border-collapse: separate;
		width: 100%;
		border-spacing: 0px 10px;
	}
	&__tr {
		display: table-row;
	}
	&__th {
		display: table-cell;
	}
	&__cell {
		background: #f9f9f9;
		vertical-align: middle;
		border: 1px solid #e3e3e3;
		border-right: none;
		padding: 5px 18px;
		margin-bottom: 10px;
		display: table-cell;
		&:last-child {
			border-right: 1px solid #e3e3e3;
		}
	}
	&__cell-name {
		width: 47%;
	}
	&__cell-size {
		width: 26.5%;
	}
	&__cell-pdf {
		width: 26.5%;
		background-color: #11a856;
		color: #ffffff;
		vertical-align: middle;
		font-weight: normal;
		text-decoration: none;
		cursor: pointer;
		&:hover {
			color: #ffffff;
		}
	}
	&__cell-link {
		width: 26.5%;
		vertical-align: middle;
		font-weight: normal;
		text-decoration: underline;
		cursor: pointer;
	}
	&__input__box {
		margin: 40px 30px 0 0;
	}
	&__button__box {
		margin: 45px 30px 0 0;
	}
	&__text__box {
		margin: 40px 0 0 0;
		width: 100%;
	}
	&__select__box #select_model,
	&__input__box #input_serial {
		min-width: 280px;
		width: 100%;
	}
	figure.img {
		margin:0 0 25px;
		text-align:center;
	}
}

@media screen and (min-width: 360px) and (max-width: 370px) {
	.c-doc__select__box #select_model,
	.c-doc__input__box #input_serial {
		min-width: 270px;
		font-size: 15px;
	}
}
@media screen and (max-width: 767px) {
	.c-doc__select__box,
	.c-doc__input__box {
		margin-right: 0;
		width: 100%;
	}
	.c-doc__button__box {
		margin-right: 0;
		width: 100%;
		text-align: center;
	}
}

.icon-pdf {
	background-image: url(../img/docs/link.png);
	background-position: -27px 0px;
	width: 28px;
	height: 25px;
	display: inline-block;
	vertical-align: middle;
	margin-right: 20px;
}
.icon-web {
	background-image: url(../img/docs/link.png);
	background-position: 0px 0px;
	width: 27px;
	height: 26px;
	margin-right: 20px;
	display: inline-block;
	vertical-align: middle;
}
.js-manual,
.js-manualActivate,
.js-manualNN,
.js-manualUpdate,
.js-manualActivate,
.js-updateCenter,
.js-clone {
	display: none;
}