.apps {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 30px;
  border: 1px solid$border-color;
  width: 100%;
  margin-bottom: 30px;
  @include transition();

  &:before {
    position: absolute;
    content: "";
    top:0;
    left:0;
    width: 100%;
    height: 2px;
    background-color: $key-color-1;
    opacity: 0;
    visibility: hidden;
  }

  @include hover() {

    .apps__text {
      u {
        color: $key-color-1;
        border-bottom: 1px solid $key-color-1;
        @include transition();
      }
    }

    &:before {
      opacity: 1;
      visibility: visible;
      @include transition();
    }
    box-shadow: 5px 11px 20px 7px hsla(0,0%,41%,.1);
    @include transition();
  }


  &__date {
    font-size: 14px;
    color: $key-color-1;
    margin-bottom: 30px;
  }

  &__text {
    color: $heading-color;
    font-weight: $default-font-weight;
    font-size: 15px;
    //margin-bottom: 30px;
    text-align: center;
    @include transition();

    u {
      text-decoration: none;
      border-bottom: 1px solid transparent;
      @include transition();
      color: $default-font-color;
    }
  }

  &__social {
    text-align: center;
    margin-bottom: 30px;

    i {
      color: #b3b3b3;
      @include transition();
    }

    i:not(:last-child) {
      margin-right: 10px;
    }

  }

  &__wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 30px;
  }


  &__price-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;


    > a.btn {
      text-transform: none !important;
      padding: 12px 35px 12px 35px;
    }


  }

  &__price {
    font-size: 36px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    text-align: center;

    > span {
      font-size: 15px;
      font-weight: $default-font-weight;
    }

    @include max-screen($xxs-max) {
      font-size: 26px;
    }


    > span.previous {
      font-size: 17px;
      font-weight: $default-font-weight-light;
      margin-right: 5px;
      text-decoration: line-through;
      color: #8a8787;
    }

  }

}


.apps-buy {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding: 30px;
  border: 1px solid $border-color;
  width: 100%;
  margin-bottom: 30px;
  @include transition();

  &:before {
    position: absolute;
    content: "";
    top:0;
    left:0;
    width: 100%;
    height: 2px;
    background-color: $key-color-1;
    opacity: 0;
    visibility: hidden;
  }

  @include hover() {

    .apps-buy__title {
      color: $key-color-1;
      @include transition();
    }

    &:before {
      opacity: 1;
      visibility: visible;
      @include transition();
    }
    box-shadow: 5px 11px 20px 7px hsla(0,0%,41%,.1);
    @include transition();
  }


  &__image {
    img {
      object-fit: scale-down;
    }
  }


  &__text {
    color: $heading-color;
    font-weight: $default-font-weight;
    font-size: 15px;
    text-align: center;
    @include transition();

    u {
      text-decoration: none;
      border-bottom: 1px solid transparent;
      @include transition();
      color: $default-font-color;
    }
  }

  &__social {
    text-align: center;
    margin-bottom: 30px;
    font-size: 20px;

    i {
      color: #b3b3b3;
      @include transition();
    }

    i:not(:last-child) {
      margin-right: 20px;
    }

  }

  &__wrapper {
    width: 50%;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
  }


  &__price-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;


    > a.btn {
      text-transform: none !important;
      padding: 12px 35px 12px 35px;
    }


  }

  &__title {
    font-size: 21px;
    font-weight: $default-font-weight;
    color: $heading-color;
    margin-bottom: 30px;
    @include transition();
  }

  &__price {
    font-size: 36px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    //margin-right: 30px;
    text-align: center;

    > span {
      font-size: 15px;
      font-weight: $default-font-weight;
    }

    @include max-screen($xxs-max) {
      font-size: 26px;
    }


    > span.previous {
      font-size: 17px;
      font-weight: $default-font-weight-light;
      margin-right: 5px;
      text-decoration: line-through;
      color: #8a8787;
    }
  }

}


