//
// .btn-group
//
$btn-group-margin: 0 -5px 30px;
$btn-group-btn-margin: 5px;


//
// .btn // default
//
$btn-margin: $clear-margin;

$btn-padding-top: 12px;
$btn-padding-right: 25px;
$btn-padding-bottom: 12px;
$btn-padding-left: 25px;

$btn-text-align: center;
$btn-text-transform: uppercase;
$btn-line-height: 1.2;
$btn-letter-spacing: $letter-spacing-wider;


$btn-font-size: 13px;
$btn-font-family: $default-font-family;
$btn-font-weight: $default-font-weight;

$btn-color: #fff;
$btn-color-hover: #fff;

$btn-bg-color: $key-color-1;
$btn-bg-color-hover: $key-color-2;

$btn-border: 2px solid transparent;
$btn-border-hover: $btn-border;
$btn-border-radius: 40px;


//
// .btn--primary
//
$btn-primary-color: #ffffff;
$btn-primary-color-hover: $heading-color;

$btn-primary-bg-color: $key-color-2;
$btn-primary-bg-color-hover: #ffffff;

$btn-primary-border: 2px solid transparent;
$btn-primary-border-hover: $btn-primary-border;


//
// .btn--outline
//
$btn-outline-color: $key-color-1;
$btn-outline-color-hover: $key-color-2;

$btn-outline-bg-color: transparent;
$btn-outline-bg-color-hover: transparent;

$btn-outline-border: 2px solid $key-color-1;
$btn-outline-border-hover: 2px solid $key-color-2;


//
// .btn--small
//
$btn-small-padding-divider: 1.5;
$btn-small-font-size: $default-font-size - 2px;

// don't edit | this code generate small size btn
$btn-small-padding-top: $btn-padding-top / $btn-small-padding-divider;
$btn-small-padding-right: $btn-padding-right / $btn-small-padding-divider;
$btn-small-padding-bottom: $btn-padding-bottom / $btn-small-padding-divider;
$btn-small-padding-left: $btn-padding-left / $btn-small-padding-divider;


//
// .btn--large
//
$btn-large-padding-divider: 1.5;
$btn-large-font-size: $default-font-size + 2px;

// don't edit | this code generate large size btn
$btn-large-padding-top: $btn-padding-top * $btn-large-padding-divider;
$btn-large-padding-right: $btn-padding-right * $btn-large-padding-divider;
$btn-large-padding-bottom: $btn-padding-bottom * $btn-large-padding-divider;
$btn-large-padding-left: $btn-padding-left * $btn-large-padding-divider;


//---[end vars]-----------------------------------------//


// btn group
.btn-group {
	@extend .flex;
	margin: $btn-group-margin;

	> .btn {
		margin: $btn-group-btn-margin;
	}
}

// default btn
.btn {
	@extend .flex, .flex--inline, .flex--center, .flex--nowrap;
	cursor: pointer;
	padding: {
		top: $btn-padding-top;
		right: $btn-padding-right;
		bottom: $btn-padding-bottom;
		left: $btn-padding-left;
	}
	margin: $btn-margin;
	text-align: $btn-text-align;
	text-decoration: none;
	text-transform: $btn-text-transform;
	line-height: $btn-line-height;
	letter-spacing: $btn-letter-spacing;
	color: $btn-color !important;
	background-color: $btn-bg-color;
	border: $btn-border;
	border-radius: $btn-border-radius;
	font-size: $btn-font-size;
	font-family: $btn-font-family;
	font-weight: $btn-font-weight;
	font-style: $default-font-style;

	@include transition();

	@include hover() {
		border: $btn-border-hover;
		text-decoration: none;
		background-color: $btn-bg-color-hover;
		color: $btn-color-hover;
	}

	// primary btn
	&--primary {
		color: $btn-primary-color !important;
		background-color: $btn-primary-bg-color;
		border: $btn-primary-border;

		@include hover() {
			color: $btn-primary-color-hover !important;
			background-color: $btn-primary-bg-color-hover;
			border: $btn-primary-border-hover;
		}
	}

	&--white {
		padding: 12px 100px 12px 100px;
		color: $key-color-1 !important;
		background-color: #ffffff;
		border: transparent;
		text-transform: none;
		font-weight: $default-font-weight;
		font-size: 16px;
		letter-spacing: 1px;

		@include max-screen($xxs-max) {
			padding: 12px 25px 12px 25px;
		}
		@include hover() {
			color: #ffffff !important;
			background-color: $key-color-3;
			border: transparent;
		}
	}

	// outline btn
	&--outline {
		color: $btn-outline-color !important;
		background-color: $btn-outline-bg-color;
		border: $btn-outline-border;

		@include hover() {
			color: $btn-outline-color-hover !important;
			background-color: $btn-outline-bg-color-hover;
			border: $btn-outline-border-hover;
		}
	}

	// small btn
	&--small {
		padding: {
			top: $btn-small-padding-top;
			right: $btn-small-padding-right;
			bottom: $btn-small-padding-bottom;
			left: $btn-small-padding-left;
		}
		font-size: $btn-small-font-size;
	}

	// large btn
	&--large {
		padding: {
			top: $btn-large-padding-top;
			right: $btn-large-padding-right;
			bottom: $btn-large-padding-bottom;
			left: $btn-large-padding-left;
		}
		font-size: $btn-large-font-size;
	}

	// block btn
	&--block {
		display: flex;
		width: 100%;
		min-width: 0;
	}


	&--red {
		color: #ffffff;
		background-color: #ff0000;
		border: transparent;

		@include hover() {
			color: $btn-outline-color-hover;
			background-color: $btn-primary-bg-color-hover;
			border: transparent;
		}
	}

	&--more {
		max-width: 120px !important;
		font-size: 13px !important;
		color: $heading-color !important;
	}


	&--nouppercase {
		text-transform: none !important;
		font-size: 16px;
		letter-spacing: 1px;

		padding: {
			top: 12px;
			right: 70px;
			bottom: 12px;
			left: 70px;
		}
	}

	&--newsletter {
		margin-top: 20px;
		display: flex;
		justify-self: flex-end;
		margin-left: auto;
		margin-right: 0;
		text-align: right;
	}
}


.login-button {

	.form__group {
		display: flex;
		align-items: center;
		justify-content: center;

		@include max-screen($xxs-max) {
			flex-direction: column;
		}

		.form__field {
			margin: 0;

			@include max-screen($xxs-max) {
				width: 100%;
			}

			> input {
				padding: 25px 25px;
			}

			&--email {
				margin: 0;
				//width: 40%;
				> input {
					border-radius: 0;
					border-bottom-left-radius: 30px;
					border-top-left-radius: 30px;

					@include max-screen($xxs-max) {
						border-bottom-left-radius: 0;
						border-top-right-radius: 30px;

					}
				}
			}

			&--password {
				margin: 0;


				> input {
					border-radius: 0;
					border-left: 0;
					//border-bottom-right-radius: 25px;
					//border-top-right-radius: 25px;

					@include max-screen($xxs-max) {

					}
				}
			}
			&--button {
				margin: 0;

				> .btn {
					padding: 25px 25px;
					border-radius: 0;
					border-bottom-right-radius: 30px;
					border-top-right-radius: 30px;

					@include max-screen($xxs-max) {
						width: 100%;
						display: flex;
						align-items: center;
						justify-content: center;
						border-top-left-radius: 0;
						border-bottom-left-radius: 30px;
						border-top-right-radius: 0;
						border-bottom-right-radius: 30px;
					}
				}
			}

		}

	}



}


