.breadcrumbs {
    display: inline-flex;
    flex-direction: row;
    font-size: 12px;
    font-weight: $default-font-weight;
    color: $default-font-color;
    align-items: center;

    padding: 10px 0;

    .is-top-minimize & {
        padding: 14px 0 8px 0;
    }

    a {
        color: inherit;
    }
    a:hover {
        color: $key-color-1;
    }

    &__title {
        padding-right: 5px;
        font-size: inherit;
        font-weight:inherit;
    }

    &__backslash {
        padding-right: 5px;
    }

    &__current {
        padding-right: 5px;
        font-size: inherit;
        text-transform: none;
        font-weight:inherit;
    }
}


.breadcrumbs-wrapper {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;

}

.breadcrumbs-return {
    font-size: 12px;
    color: $default-font-color;
    font-weight: $default-font-weight;

    .ico {
        @include transform(rotate(180deg));
        display: block;
        margin-right: 20px;
        font-size: 10px;
    }

    a {
        display: inline-flex;
        align-items: center;
        color: inherit;
        span {
            padding-left: 0;
            @include transition();
        }
    }

    a:hover {
        color: $key-color-1;
        span {
            padding-left: 10px;
            @include transition();
        }
    }
}