.box {
  width: 100%;
  border-bottom: 1px solid $border-color;
  color: #FFF;
  font-size: $default-font-size;
  padding: 60px 20px 79px 20px;
  position: relative;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
  //margin-bottom: 30px;
  overflow: hidden;
  @include transition();

  &:before {
    position: absolute;
    bottom: 0;
    content: "";
    background-color: $key-color-1;
    width: 100%;
    height: 3px;
    opacity: 0;
    visibility: hidden;
    @include transition();
  }


  &__image {
    position: relative;
    padding: 80px;
    margin-bottom: 30px;
    width: 100%;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 0;
    transition: all 1s ease;
    -moz-transition: all 1s ease;
    -ms-transition: all 1s ease;
    -webkit-transition: all 1s ease;
    -o-transition: all 1s ease;
    @include transition();

    img {
      object-fit: scale-down;
    }
  }

  &__title {
    position: absolute;
	top: 250px;
	width: 100%;
    text-align: center;
    font-size: 13px;
    font-weight: $default-font-weight-medium;
    color: $default-font-color;
    letter-spacing: $letter-spacing-wider;
    //padding-bottom: 30px;
    @include transition();
  }



  @include hover() {
    @include transition();

    .box__image {
      transform: scale(1.05);
      -moz-transform: scale(1.05);
      -webkit-transform: scale(1.05);
      -o-transform: scale(1.05);
      -ms-transform: scale(1.05); /* IE 9 */
      -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=1.5, M12=0, M21=0, M22=1.5, SizingMethod='auto expand')"; /* IE8 */
      filter: progid:DXImageTransform.Microsoft.Matrix(M11=1.05, M12=0, M21=0, M22=1.05, SizingMethod='auto expand'); /* IE6 and 7 */
    }

    &:before {
      opacity: 1;
      visibility: visible;
      @include transition();
    }


  }

}


.app-buttons {
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 100px;

  @include max-screen($sm-max) {
    flex-wrap: wrap;
  }

  &__item {
    padding: 5px 6px;

    @include transition();

    @include hover() {
      //-webkit-filter: drop-shadow(4px 4px 6px #bbb8b8);
      //filter: drop-shadow(4px 4px 6px  #bbb8b8);

      > .bounce_button {
        -webkit-transform: translateY(-15px);
        -ms-transform:     translateY(-15px);
        transform:         translateY(-15px);
        @include transition();
      }

    }
  }
}


.logo-buttons {
  [class*='col-'] {
    margin-bottom: 40px;
    @extend .flex, .flex--stretch;

    @include max-screen($xxs-max) {
      justify-content: center;
    }
  }

  &__item {
    padding: 10px 20px;
    @include transition();

    @include hover() {
      > .bounce_button {
        -webkit-transform: translateY(-15px);
        -ms-transform:     translateY(-15px);
        transform:         translateY(-15px);
        @include transition();
      }

    }
  }
}



.main .product {
  display:flex;
  @include max-screen($sm-max) {
    display: block;
  }

  &__mainpage {
    width: 46%;
    @include max-screen($sm-max) {
      width: 100%;
    }
  }

  &__image {
    display: flex;
    align-items: center;
    justify-content: center;

    @include max-screen($sm-max) {
      margin-top: 40px;
      margin-bottom: 40px;
    }
  }

  &__heading {
    font-size: 42px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    margin-bottom: 50px;
    text-align: left;
    line-height: $small-line-height;

    @include max-screen($xxs-max) {
      font-size: 26px;
    }

    h1,h2,h3,h4,h5,h6 {
      font-size: inherit;
      font-weight: inherit;
      color: inherit;
      margin: 0;
    }
  }

  &__description {
    font-size: 18px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    margin-bottom: 50px;
    text-align: left;
    line-height: 1.5;

    @include max-screen($xxs-max) {
      font-size: 16px;
    }

    h1,h2,h3,h4,h5,h6, p {
      font-size: inherit;
      font-weight: inherit;
      color: inherit;
      margin: 0;
    }
  }

  &__contact-info {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;

    .ico {
      font-size: 24px;
      padding-right: 10px;
    }
  }

  &__features {
    padding: 0 0 0 20px;
    display: flex;
    height: 100%;
    font-size: 18px;
    font-weight: $default-font-weight-light;
    color: $heading-color;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    @include max-screen($sm-max) {
      align-items: center;
      width: 100%;
    }

    @include max-screen($xxs-max) {

    }

    span.green {
      font-weight: $default-font-weight;
      font-size: 24px;
      color: $key-color-1;

      @include max-screen($xxs-max) {
        font-size: 18px;
      }


      &--18 {
        font-size: 18px;

        @include max-screen($xxs-max) {
          font-size: 14px;
        }
      }
    }


  }

  &__text-icons {
    text-align: center;
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-weight: $default-font-weight-light;
    padding: 0 40px;
    color: $heading-color;
    margin-top: 40px;

    i {
      color: $key-color-1;
      font-size: 40px;
    }

  }

  &__logo {
    width: 100%;

  }
}
.green-box-wrapper {
  align-self: flex-end;
}
.img {
  margin-left: 50px;
  margin-bottom: 50px;
}
.green-box {
  background-color: $key-color-1;
  padding: 35px 30px 30px 30px;
  margin-left: 50px;
  width:200px;
  height:200px;

  @include max-screen($sm-max) {
    width: 100%;
    height:auto;
    margin-left: 0;
  }

  @include max-screen($sm-max) {
    margin-top: 40px;
  }

  &__number {
    font-size: 48px;
    font-weight: $default-font-weight-light;
    color: #ffffff;
    margin-bottom: 15px;
    text-align: left;
  }

  &__text {
    font-size: 16px;
    font-weight: $default-font-weight;
    color: #ffffff;
    text-align: left;

    @include max-screen($md-max) {
      //font-size: 18px;
    }

  }

  @include max-screen($xxs-max) {
    margin-bottom: 40px;
  }
}


.icon-text {
  padding: 100px 0 0 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $heading-color;

  @include max-screen($sm-max) {
    padding: 50px;
  }

  @include max-screen($xxs-max) {
    padding: 20px;
  }

  &__icon {
    background-color: #ffffff;
    border: 1px solid $key-color-1;
    width: 78px;
    height: 78px;
    border-radius: 39px;
    color: $key-color-1;
    //font-weight: $default-font-weight-bold;
    font-size: 20px;
    padding: 0;
    //margin-bottom: 40px;

    @include transition();

    /* position */
    display: flex;
    align-items: center;
    justify-content: center;

    i {
      color: inherit;
      font-weight: $default-font-weight-bold;
      padding: 0;
      font-size: 30px;
    }


    @include hover() {
      background-color: $key-color-1;
      color: #ffffff;
      @include transition();
    }

    &--timer {
      width: 60px;
      height: 58px;
      border-radius: 30px;
    }

    &_pnd {
      background: #fff url('../../assets/img/78x78_pnd_white.svg');
      background-size: 76px;
      &:hover {
        background: url('../../assets/img/78x78_pnd_green.svg');
      }
    }

    &_dvr {
      background: #fff url('../../assets/img/78x78_dvr_white.svg');
      background-size: 76px;
      &:hover {
        background: url('../../assets/img/78x78_dvr_green.svg');
      }
    }

    &_mob {
      background: #fff url('../../assets/img/78x78_android_white.svg');
      background-size: 76px;
      &:hover {
        background: url('../../assets/img/78x78_android_green.svg');
      }
    }
  }

  &__green-icon {
      margin-bottom: 30px;
    .ico {
      font-size: 60px;
      color: $key-color-1;
    }
  }

  &__text {
    padding: 0 30px;
    font-size: 18px;
    text-align: center;
    font-weight: $default-font-weight;
    width: 100%;
  }
}



@keyframes bounce {
  0%, 100%, 20%, 50%, 80% {
    -webkit-transform: translateY(0);
    -ms-transform:     translateY(0);
    transform:         translateY(0)
  }
  40% {
    -webkit-transform: translateY(-30px);
    -ms-transform:     translateY(-30px);
    transform:         translateY(-30px)
  }
  60% {
    -webkit-transform: translateY(-15px);
    -ms-transform:     translateY(-15px);
    transform:         translateY(-15px)
  }
}
.bounce_button {
  //position: absolute;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  -webkit-transform: translateY(0);
  -ms-transform:     translateY(0);
  transform:         translateY(0);
  @include transition();
}
