
.grid {
  position: relative;
  margin: 0 auto;
  list-style: none;
  text-align: center;
}

/* Common style */
.grid figure {
  position: relative;
  float: left;
  overflow: hidden;
  height: auto;
  width: 100%;
  background: #000000;
  text-align: center;
  cursor: pointer;

  @include max-screen($md-max) {
    margin-bottom: 10px;
  }
}

.grid figure img {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  opacity: 0.7;
}

.grid figure figcaption {
  padding: 30px;
  color: #fff;
  font-size: 1.25em;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;


  @include max-screen($xxs-max) {
    padding: 30px;
  }


  span.heading {
    font-size: 13px;
    letter-spacing: $letter-spacing-wider;
    text-transform: uppercase;
  }
}

.grid figure figcaption::before,
.grid figure figcaption::after {
  pointer-events: none;
}

.grid figure figcaption,
.grid figure figcaption > a {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* Anchor will cover the whole item by default */
/* For some effects it will show as a button */
.grid figure figcaption > a {
  z-index: 1000;
  text-indent: 200%;
  white-space: nowrap;
  font-size: 0;
  opacity: 0;
}

.grid figure h3 {
  word-spacing: -0.15em;
  font-weight: 300;
  font-size: 20px;
  color: #ffffff;
  text-align: left;
  line-height: 1;
  letter-spacing: $letter-spacing-wider;

  @include max-screen($lg-max) {
    font-size: 24px;
  }
  @include max-screen($md-max) {
    font-size: 30px;
  }

  @include max-screen($xxs-max) {
    font-size: 26px;
  }
}

.grid figure h3 span {
  font-weight: 800;
}

.grid figure h3,
.grid figure p {
  margin: 0;
}

.grid figure p {
  letter-spacing: 1px;
  font-size: 68.5%;
}



/*---------------*/
/***** Marley *****/
/*---------------*/

figure.effect-marley figcaption {
  text-align: right;
}

figure.effect-marley h3,
figure.effect-marley p {
  position: absolute;
  display: block;
  right: 15px;
  left: 30px;
  padding: 10px 0;

  @include max-screen($xxs-max) {
    left: 25px;
  }
}

figure.effect-marley span.heading {
  position: absolute;
  left: 32px;
  -webkit-transition: -webkit-transform 0.35s;
  transition: transform 0.35s;
  -webkit-transform: translate3d(0,0px,0);
  transform: translate3d(0,0px,0);

  @include max-screen($xxs-max) {
    left: 26px;
  }
}
figure.effect-marley:hover span.heading {
  -webkit-transform: translate3d(0,-20px,0);
  transform: translate3d(0,-20px,0);
}

figure.effect-marley p {
  bottom: 40px;
  line-height: 1.5;
  -webkit-transform: translate3d(0,100%,0);
  transform: translate3d(0,100%,0);
}

figure.effect-marley h3 {
  top: 56px;
  -webkit-transition: -webkit-transform 0.35s;
  transition: transform 0.35s;
  -webkit-transform: translate3d(0,20px,0);
  transform: translate3d(0,20px,0);

  @include max-screen($xxs-max) {
    top: 16px;
  }
}

figure.effect-marley:hover h3 {
  -webkit-transform: translate3d(0,0,0);
  transform: translate3d(0,0,0);
}

figure.effect-marley h3::after {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 4px;
  background: #fff;
  content: '';
  -webkit-transform: translate3d(0,40px,0);
  transform: translate3d(0,40px,0);
}

figure.effect-marley h3::after,
figure.effect-marley p {
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
}

figure.effect-marley:hover h3::after,
figure.effect-marley:hover p {
  opacity: 1;
  -webkit-transform: translate3d(0,0,0);
  transform: translate3d(0,0,0);
}